import { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { Box, Container } from '@material-ui/core';
import AdministradorListResults from 'src/components/administradores/AdministradorListResults';
import AdministradorListToolbar from 'src/components/administradores/AdministradorListToolbar';
import AxiosAux from 'src/hooks/UseAxios';
import { useDispatch, useSelector } from 'react-redux';
import { startFetchAll } from 'src/reducers/administradores/actionCreator';
import Notification from 'src/components/Notification';

const AdministradorList = () => {
  const dispatch = useDispatch();
  const { alert } = useSelector(state => state.ui);
  const { doRequest } = AxiosAux('admin');  
 

  useEffect(() =>{
    (async () => {
      dispatch(startFetchAll(doRequest));
    })()
  }, []);
  
  
  return (
  <>
    <Helmet>
      <title>Administradores</title>
    </Helmet>
    <Box
      sx={{
        backgroundColor: 'background.default',
        minHeight: '100%',
        py: 3
      }}
    >
      <Container maxWidth={false}>
        <AdministradorListToolbar/>
        <Box sx={{ pt: 3 }}>
         
         <AdministradorListResults/>
         
        </Box>
      </Container>
      <Notification alert={alert}></Notification>
    </Box>
  </>
)
};

export default AdministradorList;
