import { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { Box, Container } from '@material-ui/core';
import DepartamentoListResults from 'src/components/departamentos/DepartamentoListResults';
import DepartamentoListToolbar from 'src/components/departamentos/DepartamentoListToolbar';
import AxiosAux from 'src/hooks/UseAxios';
import { useDispatch, useSelector } from 'react-redux';
import { startFetchAll } from 'src/reducers/departamentos/actionCreator';
import Notification from 'src/components/Notification';

const DepartamentoList = () => {
  const dispatch = useDispatch();
  const { alert } = useSelector(state => state.ui);
  const { doRequest } = AxiosAux();  
 

  useEffect(() =>{
    (async () => {
      dispatch(startFetchAll(doRequest));      
    })()
  }, []);
  
  
  return (
  <>
    <Helmet>
      <title>
        Departamentos
      </title>
    </Helmet>
    <Box
      sx={{
        backgroundColor: 'background.default',
        minHeight: '100%',
        py: 3
      }}
    >
      <Container maxWidth={false}>
        <DepartamentoListToolbar/>
        <Box sx={{ pt: 3 }}>
         
         <DepartamentoListResults/>
         
        </Box>
      </Container>
      <Notification alert={alert}></Notification>
    </Box>
  </>
)
};

export default DepartamentoList;
