import React from 'react'
import propTypes from "prop-types";
import {Select , MenuItem, FormControl, InputLabel, FormHelperText } from '@material-ui/core';


export default function SelectControl(props) {
    
    const {name, label, value, error = null, onChange, options, onBlur, helperText } = props;    
    const rendered = options.map(item =>{    
        return   <MenuItem key={item.id} value={item.id}>{item.text}</MenuItem>                                               
    });

    return (
        <FormControl variant="outlined"  style={{marginTop:'16px'}}
        {...(error && {error:true})}>
            <InputLabel id="demo-simple-select-outlined-label">{label}</InputLabel>
            <Select
                    labelId="demo-simple-select-outlined-label"
                    value={value}
                    onChange={onChange}
                    onBlur={onBlur}
                    label={label}
                    name={name}                   
                    >
                    <MenuItem key={-1} value={-1}> -- Seleccione una opción -- </MenuItem>
                    {rendered}
            </Select>
        {error && <FormHelperText>{helperText}</FormHelperText>}
        </FormControl>
        );        
}

SelectControl.propTypes = {
    name: propTypes.string.isRequired,
    label: propTypes.string.isRequired,
    value: propTypes.number.isRequired,
    onChange: propTypes.func.isRequired,
    onBlur: propTypes.func.isRequired,
    options: propTypes.array.isRequired
};

