const accions = {        
    EMPLEADOS_FETCH: '[EMPLEADOS]fetch all EMPLEADOS',
    EMPLEADOS_UPDATE: '[EMPLEADOS] update empleado',
    EMPLEADOS_CREATE: '[EMPLEADOS] create empleado',
    EMPLEADOS_SET_ERROR: '[EMPLEADOS] error',        
    EMPLEADOS_SET_CHECKING: '[EMPLEADOS] fetching...',
    EMPLEADOS_SET_FILTER: '[EMPLEADOS] set value to filter EMPLEADOS',
    EMPLEADOS_DISABLE: '[EMPLEADOS] disable empleado',
    EMPLEADOS_ENABLE: '[EMPLEADOS] enable empleado',
    ESTADO_FETCH: '[ESTADO] fetch all',
        
    fetchAll: (empleados) => {
        return{
            type: accions.EMPLEADOS_FETCH,
            payload: empleados
        }
    },

    fetchAllEstado: (estados) => {
        return{
            type: accions.ESTADO_FETCH,
            payload: estados
        }
    },

    create: (empleado) => {
        return{
            type: accions.EMPLEADOS_CREATE,
            payload: empleado
        }
    },
    update: (empleado) => {
        return{
            type: accions.EMPLEADOS_UPDATE,
            payload: empleado
        }
    },
    
    setChecking: (value) => {
        return {
            type: accions.EMPLEADOS_SET_CHECKING,
            payload: value
        }        
    },    
    
    setError: (error) =>{
        return {
            type: accions.EMPLEADOS_SET_ERROR,
            payload: error
        }
    },
    setFilter: (value) =>{
        return {
            type: accions.EMPLEADOS_SET_FILTER,
            payload: value
        }
    },
    disable: (value) =>{
        return {
            type: accions.EMPLEADOS_DISABLE,
            payload: value
        }
    },
    enable: (value) =>{
        return {
            type: accions.EMPLEADOS_ENABLE,
            payload: value
        }
    }
    
}

export default accions;