import { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  CircularProgress
} from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import StyledButton from 'src/components/controls/StyledButton';
import UsePagination from "src/hooks/UsePagination";
import StyledDialog from '../Dialog';
import AdministradorForm from './AdministradorForm';
import AxiosAux from 'src/hooks/UseAxios';
import ConfirmDialog from '../ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { setConfirmDialog, startSetAlert } from 'src/reducers/ui/actionCreator';
import { startDisable, startEnable } from "src/reducers/administradores/actionCreator";
import { dateFormater } from 'src/helpers/formatters';
import handleRequestError from 'src/helpers/handleReqErrors';

const useStyles = makeStyles(() => ({
  child:{
    
  },
  root:{
    textAlign: 'center'
  }
}));

const AdministradorListResults = ({...rest }) => {
  const dispatch = useDispatch();
  const { administradores, filterValue, fetching } = useSelector(state => state.administradores);      
  const [open, setOpen] = useState(false);
  const [filteredAdmins, setFilteredAdmins] = useState([]);
  const [childrenDialog, setChildrenDialog] = useState({ title: "", children: null });
  const classes = useStyles();

  const {
    handleLimitChange,
    handlePageChange,
    limit,
    page
  } = UsePagination();

  const {
    doRequest
  } = AxiosAux();

  const filter = (value) => {
    if (value !== "") {
      const filtered = administradores.filter(
        x =>
          x.id === value ||
          x.persona.nombre.toLowerCase().includes(value) ||
          x.persona.primerAp.toLowerCase().includes(value) ||
          x.usuario.email.toLowerCase().includes(value)
      );
      setFilteredAdmins(filtered);
    } else {
      setFilteredAdmins(administradores);
    }

  }

  const openDialog = (administrador) => {
    const title = 'Actualizar Administrador';
    //Se formatea la estructura del objeto del administrador
    if (administrador.persona.segundoAp === null)
      administrador.persona.segundoAp = '';

    administrador.usuario.confirmPassword = '';
    administrador.usuario.password = '';
    setChildrenDialog({
      title: title,
      children: <AdministradorForm setOpen={setOpen} title={title} edit={administrador} ></AdministradorForm>
    });

  }

  const startDeleteOrEnable = (id, estatus, nombre) => {
    dispatch(setConfirmDialog({
      open: true,
      title: estatus === '1' ? `Desea Deshabilitar al Administrador ${nombre}?` : `Desea Habilitar al Administrador ${nombre}?`,
      subtitle: "Está Acción puede ser Revertida",      
      onConfirm: () => { handleDeleteOrEnable(id, estatus); dispatch(setConfirmDialog({inProgress: true})) }
    }));
  }

  const handleDeleteOrEnable = async (id, estatus) => {
    try {
      if (estatus === '1') {
        await dispatch(startDisable(id, doRequest));
      } else {
        await dispatch(startEnable(id, doRequest));
      }               

    } catch (err) {      
      if (err?.response?.status !== 500) {
        const stringError = handleRequestError(err);
        dispatch(setError(stringError));
      }
      dispatch(startSetAlert({
          type: 'error',
          isOpen: true,
          title: 'Error',
          message: "Error del Servidor, Intentelo de Nuevo"
          }));       
      dispatch(setConfirmDialog({inProgress: false, open: false}));
      }
  }

  useEffect(() => {
    if (childrenDialog.children !== null) {
      setOpen(true);
    }
  }, [childrenDialog]);

  useEffect(() => {
    if (administradores) {
      setOpen(false);
      setFilteredAdmins(administradores)
    }
  }, [administradores]);

  useEffect(() => {
    if (filterValue !== null) {
      filter(filterValue);
    }
  }, [filterValue]);



  return (
    <Card {...rest}>
     
      <PerfectScrollbar>
        <Box sx={{ minWidth: 1050 }}>
         
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Clave Administrador
                </TableCell>
                <TableCell>
                  Nombre
                </TableCell>
                <TableCell>
                  Correo Electrónico
                </TableCell>
                <TableCell>
                  Teléfono
                </TableCell>
                <TableCell>
                  Fecha de Registro
                </TableCell>
                <TableCell>
                  Permisos
                </TableCell>
                <TableCell>
                  Estatus
                </TableCell>
                <TableCell>
                  Acciones
                </TableCell>
              </TableRow>
            </TableHead>
            
            <TableBody>      
               
              {
                filteredAdmins.slice(page * limit, (page + 1) * limit).map(administrador => (
                  <TableRow key={administrador.id}>
                    <TableCell>{administrador.id}</TableCell>
                    <TableCell>{administrador.persona.nombre} {administrador.persona.primerAp} {administrador.persona.segundoAp}</TableCell>
                    <TableCell>{administrador.usuario.email}</TableCell>
                    <TableCell>{administrador.persona.telefono}</TableCell>
                    <TableCell>{dateFormater(administrador.persona.createdAt)}</TableCell>
                    
                    <TableCell>{administrador.usuario.nivelAcceso === 0 ? <Typography variant="button" color="darkslateblue">Super Admin</Typography>: <Typography variant="button" color="lightcoral">Titular</Typography>}</TableCell>
                    <TableCell>{administrador.estatus === '1' ? <Typography variant='button' color="green">Activo</Typography> : <Typography variant='button' color="red">No Activo</Typography>}</TableCell>
                    <TableCell>
                      <StyledButton color='primary' onClick={() => openDialog(administrador)} endIcon={<CreateIcon></CreateIcon>}> Editar </StyledButton>
                      {
                        administrador.estatus === '1' ?
                          <StyledButton color='error' onClick={() => startDeleteOrEnable(administrador.id, administrador.estatus, administrador.persona.nombre)} endIcon={<DeleteIcon></DeleteIcon>}> Deshabilitar </StyledButton>
                          :
                          <StyledButton color='info' onClick={() => startDeleteOrEnable(administrador.id, administrador.estatus, administrador.persona.nombre)} endIcon={<ArrowUpward></ArrowUpward>}> Habilitar </StyledButton>
                      }

                    </TableCell>
                  </TableRow>
                ))
              }
            </TableBody>
            
          </Table>
          {
               fetching && 
                <div className={classes.root}>
                  <CircularProgress color="primary"/>    
                </div>                                
            }  
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredAdmins.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
      <StyledDialog
        title={childrenDialog.title}
        open={open}
        setOpen={setOpen}
      >
        {childrenDialog.children}
      </StyledDialog>
      <ConfirmDialog>
      </ConfirmDialog>
    </Card>
  );
};


export default AdministradorListResults;
