import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
    Box,
    Typography,
    Grid    
} from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import StyledButton from 'src/components/controls/StyledButton'
import StyledTextField from 'src/components/controls/StyledTextField'
import AxiosAux from 'src/hooks/UseAxios';
import { useDispatch, useSelector } from 'react-redux';
import { startCreate, startUpdate, setError } from 'src/reducers/categorias/actionCreator';
import { startSetAlert } from 'src/reducers/ui/actionCreator';
import handleRequestError from 'src/helpers/handleReqErrors';

const initialValues = {
    id: 0,
    nombre: '',
    descripcion: ''
};

const CategoriaForm = ({ title, edit = null }) => {    
    const dispatch = useDispatch();
    const { error } = useSelector(state => state.categorias);
    const { 
        doRequest
    } = AxiosAux();

    const validate = () => {
       return Yup.object().shape({
            nombre: Yup.string().min(4, "Longitud Mínima 4 Letras").required('Nombre es Requerido'),
            descripcion: Yup.string().min(4, "Longitud Mínima 4 Letras").required('Descripción es Requerida')
        });
    }

    const handleSubmit = async (values, setFieldError) => {        
        try {                                 
            let message = '';
            if(edit){                
                await dispatch(startUpdate(edit.id, values, doRequest));                                
                message = 'Categoría Actualizada Correctamente';
            }else{                            
                await dispatch(startCreate(values, doRequest)); 
                message = 'Categoría Registrada Correctamente';                   
            }                                         
            dispatch(startSetAlert({
                isOpen: true,
                title: 'Éxito',
                message: message
            }));
        } catch (err) {                        
            if (err?.response?.status !== 500) {
                const stringError = handleRequestError(err, setFieldError);
                dispatch(setError(stringError));
            }
            dispatch(startSetAlert({
                type: 'error',
                isOpen: true,
                title: 'Error',
                message: "Error del Servidor, Intentelo de Nuevo"
                }));                  
        }
    }  
    
    useEffect(() => {
        dispatch(setError(''));                
    }, []);

    return (        
        <Formik
            initialValues={edit ? edit : initialValues}   
            validationSchema={
                validate()
            }
            validateOnChange={true}    
            onSubmit={(values, { setFieldError }) => handleSubmit(values, setFieldError)}
        >
            {({
                errors,                
                handleBlur,
                handleChange,
                handleSubmit,
                isSubmitting,
                touched,
                values
            }) => (
                <form onSubmit={handleSubmit}>
                    <Box sx={{ mb: 3 }}>
                        <Typography
                            color="textPrimary"
                            variant="h2"
                        >
                            {title}
                        </Typography>
                    </Box>
                    <Grid container spacing={1}>
                        <Grid item xs={12}>                         
                            <StyledTextField
                                error={Boolean(touched.nombre && errors.nombre)}
                                helperText={touched.nombre && errors.nombre}
                                label="Nombre"
                                disabled={Boolean(edit)}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="nombre"
                                value={values.nombre}
                            />
                            <StyledTextField
                                error={Boolean(touched.descripcion && errors.descripcion)}
                                helperText={touched.descripcion && errors.descripcion}
                                label="Descripción"                             
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="descripcion"
                                value={values.descripcion}
                            />
                        </Grid>
                    </Grid>
                    <Box sx={{ py: 2 }}>
                        <StyledButton
                            color='primary'
                            type='submit'
                            startIcon={<SaveIcon></SaveIcon>}
                            disabled={isSubmitting}
                            variant={'contained'}
                        >
                            Registrar
                        </StyledButton>
                        <Typography color="error" align="center" style={{ marginTop: '10px' }}>
                            {error}
                        </Typography>                        
                    </Box>
                </form>
            )}
        </Formik>
    )
}

CategoriaForm.propTypes = {
    title: PropTypes.string.isRequired,    
};

export default CategoriaForm;
