import { useEffect } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Button,
  Divider,
  Drawer,  
  List,
  Typography
} from '@material-ui/core';
import {

  BarChart as BarChartIcon,
  User as UserIcon,
  Users as UsersIcon,
  Tag,
  Video,
  Grid
} from 'react-feather';
import NavItem from './NavItem';
import { useSelector } from 'react-redux';

const items = [
  {
    href: '/app/dashboard',
    icon: BarChartIcon,
    title: 'Dashboard'
  },
  {
    href: '/app/administradores',
    icon: UsersIcon,
    title: 'Administradores'
  },
  {
    href: '/app/empleados',
    icon: UserIcon,
    title: 'Empleados'
  },
  {
    href: '/app/categorias',
    icon: Tag,
    title: 'Categorías'
  },
  {
    href: '/app/capitulos',
    icon: Video,
    title: 'Capítulos'
  },
  {
    href: '/app/departamentos',
    icon: Grid,
    title: 'Departamentos'
  },
  {
    href: '/app/perfil',
    icon: UserIcon,
    title: 'Perfil'
  }
];

const DashboardSidebar = ({ onMobileClose, openMobile }) => {
  const location = useLocation();
  const { user } = useSelector(state => state.auth);

  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
  }, [location.pathname]);

  const content = (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%'
      }}
    >
      <Box
        sx={{
          alignItems: 'center',
          display: 'flex',
          flexDirection: 'column',
          p: 2
        }}
      >
        <Avatar
          component={RouterLink}
          src={'/static/images/avatars/avatar.png'}
          sx={{
            cursor: 'pointer',
            width: 64,
            height: 64
          }}
          to="/app/perfil"
        />
        <Typography
          color="textPrimary"
          variant="h5"
        >
          {user.email}
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {user.tipo}
        </Typography>
      </Box>
      <Divider />
      <Box sx={{ p: 2 }}>
        <List>
          {items.map((item) => (
            
            user.nivelAcceso === 0 ? 
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
            :
            item.href !== '/app/administradores' && user.nivelAcceso === 1 &&
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />

          ))}
        </List>
      </Box>
    </Box>
  );

  return (
    <>
      <Box display={{ lg: 'none' }}>
        <Drawer
          anchor="left"
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
          PaperProps={{
            sx: {
              width: 256
            }
          }}
        >
          {content}
        </Drawer>
      </Box>
      <Box display={{ xs: 'none', lg: 'block' }}>
        <Drawer
          anchor="left"
          open
          variant="persistent"
          PaperProps={{
            sx: {
              width: 256,
              top: 64,
              height: 'calc(100% - 64px)'
            }
          }}
        >
          {content}
        </Drawer>
      </Box>
    </>
  );
};

DashboardSidebar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

DashboardSidebar.defaultProps = {
  onMobileClose: () => { },
  openMobile: false
};

export default DashboardSidebar;
