const accions = {        
    UI_SET_ALERT: '[ui] set alert',
    UI_SET_CONFIRM_DIALOG: '[ui] set confirm dialog',
    
    setAlert: (payload) => {
        return{
            type: accions.UI_SET_ALERT,
            payload: payload
        }
    },
    setConfirmDialog: (payload) => {
        return{
            type: accions.UI_SET_CONFIRM_DIALOG,
            payload: payload
        }
    }
}

export default accions;