import { Button } from '@material-ui/core';
import PropTypes from 'prop-types';

export default function AccionButton(props) {

    const { color, children, onClick, size = 'medium', disabled = false, ...others } = props;

    return (
        <Button
            color={color}
            {...(onClick && {onClick:onClick})}
            disabled={disabled}
            size={size}
            {...others}
        >
            {children}
        </Button>
    )
}

AccionButton.propTypes = {
    color: PropTypes.string.isRequired    
};

