import { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  CircularProgress
} from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import StyledButton from 'src/components/controls/StyledButton';
import UsePagination from "src/hooks/UsePagination";
import AxiosAux from 'src/hooks/UseAxios';
import ConfirmDialog from '../ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { setConfirmDialog, startSetAlert } from 'src/reducers/ui/actionCreator';
import { setError, startDisable, startEnable } from "src/reducers/empleados/actionCreator";
import handleRequestError from 'src/helpers/handleReqErrors';
import { cleanEmpleados, startDisableAsigEmpleado, startFetchDepartamentoEmpleados } from 'src/reducers/departamentos/actionCreator';

const useStyles = makeStyles(() => ({
  child:{
    
  },
  root:{
    textAlign: 'center'
  }
}));

const EmpleadoTable = ({ id, ...rest }) => {

  const dispatch = useDispatch();
  const { empleados, fetching } = useSelector(state => state.departamentos);        
  const [filteredEmpleados, setFilteredEmpleados] = useState([]);  
  const classes = useStyles();

  const {
    handleLimitChange,
    handlePageChange,
    limit,
    page
  } = UsePagination();

  const {
    doRequest
  } = AxiosAux();

  const filter = (value) => {
    if (value !== "") {
      const filtered = empleados.filter(
        x =>
          x.id === value ||
          x.persona.nombre.toLowerCase().includes(value) ||
          x.persona.primerAp.toLowerCase().includes(value) ||
          x.usuario.email.toLowerCase().includes(value)
      );
      setFilteredEmpleados(filtered);
    } else {
      setFilteredEmpleados(empleados);
    }

  }
  
  const startDeleteOrEnableAsignacion = (id, nombre) => {
    dispatch(setConfirmDialog({
      open: true,
      title: `Desea Deshabilitar la Asignación del empleado ${nombre}?`,
      subtitle: "Está Acción no puede ser Revertida",      
      onConfirm: () => { handleDeleteOrEnableAsignacion(id); dispatch(setConfirmDialog({inProgress: true})) }
    }));
  }

  const handleDeleteOrEnableAsignacion = async (id) => {
    try {
      
        await dispatch(startDisableAsigEmpleado(id, doRequest));                                 

    } catch (err) {
      
      if (err?.response?.status !== 500) {
        const stringError = handleRequestError(err);
        dispatch(setError(stringError));
      }
      dispatch(startSetAlert({
        type: 'error',
        isOpen: true,
        title: 'Error',
        message: "Error del Servidor, Intentelo de Nuevo"
      }));       
      dispatch(setConfirmDialog({inProgress: false, open: false}));
    }
  }

  useEffect(() => {
    if (empleados) {      
      setFilteredEmpleados(empleados);
    }
 
  }, [empleados]);

  useEffect(() =>{
    dispatch(startFetchDepartamentoEmpleados(id, doRequest));
    return () =>{
      dispatch(cleanEmpleados());
    }
  }, []);

  if(fetching){
    return (
      <div className={classes.root}>
        <CircularProgress color="primary"/>    
      </div> 
    );
  }
  
  if(filteredEmpleados.length === 0){
    return (
      <div style={{ display: 'flex', alignItems:"center", flexDirection:"column" }}>
        <Typography textAlign="center" variant="button" color="primary">Sin Empleados Asignados</Typography>
      </div>      
    )
  }

  return (
    <Card {...rest}>    
      <PerfectScrollbar>
        <Box sx={{ minWidth: 1050, minHeight: 100 }}>         
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Clave empleado
                </TableCell>
                <TableCell>
                  Nombre
                </TableCell>                                              
                <TableCell>
                  Acciones
                </TableCell>
              </TableRow>
            </TableHead>            
            <TableBody>                     
              {                

                filteredEmpleados.slice(page * limit, (page + 1) * limit).map(empleado => (
                  <TableRow key={empleado.id}>
                    <TableCell>{empleado.id} <br></br>{empleado.noInterno}</TableCell>
                    <TableCell>{empleado.persona.nombre} {empleado.persona.primerAp} {empleado.persona.segundoAp}</TableCell>                              
                    <TableCell>                                                                                      
                      <StyledButton color='error' onClick={() => startDeleteOrEnableAsignacion(empleado.Departamento[0].Departamento_Empleado.id,  empleado.persona.nombre)} endIcon={<DeleteIcon></DeleteIcon>}> Eliminar Asignación </StyledButton>                                                                        
                    </TableCell>
                  </TableRow>
                ))
              }
            </TableBody>              
          </Table>          
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredEmpleados.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />  
      <ConfirmDialog>
      </ConfirmDialog>
    </Card>
  );
};


export default EmpleadoTable;
