import accions from './actions';

const initState = {
  administradores: [],
  filterValue: null,
  fetching: true,
  message: "",
  error: ""  
};

/**
 *
 * @todo impure state mutation/explaination
 */
const AdministradoresReducer = (state = initState, action) => {

  const { type, payload } = action;  
  switch (type) {
    
    case accions.ADMINS_FETCH:      
      return {
          ...state,
          administradores: payload,
          fetching: false
        };
    case accions.ADMINS_CREATE:     
        return {
          ...state,
          administradores: [...state.administradores, { ...payload }]          
        };
    case accions.ADMINS_UPDATE:     
        return {
          ...state,
          administradores: state.administradores.map(
            admin => ( admin.id === payload.id ? { ...payload } : admin )
          )        
        };
    case accions.ADMINS_SET_ERROR:
  
      return {
          ...state,
          error: payload,
          fetching: false
        };  
    case accions.ADMINS_SET_FILTER:
        return {
          ...state,
          filterValue: payload
        }
    case accions.ADMINS_DISABLE:
      return {
        ...state,
        administradores: state.administradores.map(
          admin => ( admin.id === payload ? { ...admin, estatus: '0' } : admin )
        )  
      }

    case accions.ADMINS_ENABLE:
        return {
          ...state,
          administradores: state.administradores.map(
            admin => ( admin.id === payload ? { ...admin, estatus: '1' } : admin )
          )  
        }
     
    default:
      return state;
  }
};
export default AdministradoresReducer;
