import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
    Box,
    Typography,
    Grid    
} from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import StyledButton from 'src/components/controls/StyledButton'
import StyledTextField from 'src/components/controls/StyledTextField'
import AxiosAux from 'src/hooks/UseAxios';
import { useDispatch, useSelector } from 'react-redux';
import { startCreate, startUpdate, setError, startFetchDepartamentoCategorias, cleanDeparCategorias } from 'src/reducers/departamentos/actionCreator';
import { startSetAlert } from 'src/reducers/ui/actionCreator';
import handleRequestError from 'src/helpers/handleReqErrors';
import { startFetchAll } from 'src/reducers/categorias/actionCreator';
import MultiSelect from '../controls/MultiSelect';

const initialValues = {
    id: 0,
    nombre: '',
    descripcion: '',
    categorias: []
};

const DepartamentoForm = ({ title, edit = null }) => {    
    const dispatch = useDispatch();
    const { error, deparCategorias } = useSelector(state => state.departamentos);
    const { categorias } = useSelector(state => state.categorias);
    const [categoriasFormatted, setCategoriasFormatted] = useState([]);
    const { 
        doRequest
    } = AxiosAux();

    const validate = () => {
       return Yup.object().shape({
            nombre: Yup.string().min(4, "Longitud Mínima 4 Letras").required('Nombre es Requerido'),
            descripcion: Yup.string().min(4, "Longitud Mínima 4 Letras").required('Descripción es Requerida')
        });
    }

    const handleSubmit = async (values, setFieldError) => {        
        try {                                       
            let message = '';            
            if(edit){                
                await dispatch(startUpdate(edit.id, values, doRequest));                                
                message = 'Departamento Actualizado Correctamente';
            }else{                            
                await dispatch(startCreate(values, doRequest)); 
                message = 'Departamento Registrado Correctamente';                   
            }                                         
            dispatch(startSetAlert({
                isOpen: true,
                title: 'Éxito',
                message: message
            }));
        } catch (err) {  
            console.log(err)                      
            if (err?.response?.status !== 500) {
                const stringError = handleRequestError(err, setFieldError);
                dispatch(setError(stringError));
            }
            dispatch(startSetAlert({
                type: 'error',
                isOpen: true,
                title: 'Error',
                message: "Error del Servidor, Intentelo de Nuevo"
                }));                  
        }
    }  

    useEffect(() =>{
        if(categorias){
            setCategoriasFormatted(categorias.map(cat => (
                cat.estatus === '1' && { id: cat.id, text: cat.nombre }
            )));
        }        
    }, [categorias]);   
    
    useEffect(() => {        
        if(edit !== null){            
            dispatch(startFetchDepartamentoCategorias(edit.id, doRequest));
        }
        return () =>{
            dispatch(cleanDeparCategorias());
        }
    }, [edit]);

    useEffect(() => {
        dispatch(setError(''));    
        dispatch(startFetchAll(doRequest));    
    }, []);

    return (        
        <Formik
            initialValues={edit ? edit : initialValues}   
            validationSchema={
                validate()
            }
            validateOnChange={true}    
            onSubmit={(values, { setFieldError }) => handleSubmit(values, setFieldError)}
        >
            {({
                errors,                
                handleBlur,
                handleChange,
                handleSubmit,
                isSubmitting,
                touched,
                values,
                setFieldValue,
                setFieldError
            }) => (
                <form onSubmit={handleSubmit}>
                    <Box sx={{ mb: 3 }}>
                        <Typography
                            color="textPrimary"
                            variant="h2"
                        >
                            {title}
                        </Typography>
                    </Box>
                    <Grid container spacing={1}>
                        <Grid item xs={12}>                         
                            <StyledTextField
                                error={Boolean(touched.nombre && errors.nombre)}
                                helperText={touched.nombre && errors.nombre}
                                label="Nombre"
                                disabled={Boolean(edit)}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="nombre"
                                value={values.nombre}
                            />
                            <StyledTextField
                                error={Boolean(touched.descripcion && errors.descripcion)}
                                helperText={touched.descripcion && errors.descripcion}
                                label="Descripción"                             
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="descripcion"
                                value={values.descripcion}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <MultiSelect
                                error={Boolean(touched.categorias && errors.categorias)}
                                helperText={touched.categorias && errors.categorias}
                                label='Categorías'
                                setFieldError={setFieldError}
                                setFieldValue={setFieldValue}
                                options={categoriasFormatted}                                
                                onBlur={handleBlur}
                                name="categorias"
                                defaultValues={edit ? deparCategorias : []}                                
                            >
                            </MultiSelect>
                        </Grid>
                    </Grid>
                    <Box sx={{ py: 2 }}>
                        <StyledButton
                            color='primary'
                            type='submit'
                            startIcon={<SaveIcon></SaveIcon>}
                            disabled={isSubmitting}
                            variant={'contained'}
                        >
                            Registrar
                        </StyledButton>
                        <Typography color="error" align="center" style={{ marginTop: '10px' }}>
                            {error}
                        </Typography>                        
                    </Box>
                </form>
            )}
        </Formik>
    )
}

DepartamentoForm.propTypes = {
    title: PropTypes.string.isRequired,    
};

export default DepartamentoForm;
