import { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { Box, Container } from '@material-ui/core';
import EmpleadoListResults from 'src/components/empleados/EmpleadoListResults';
import EmpleadoListToolbar from 'src/components/empleados/EmpleadoListToolbar';
import AxiosAux from 'src/hooks/UseAxios';
import { useDispatch, useSelector } from 'react-redux';
import { startFetchAll, startFetchAllEstado } from 'src/reducers/empleados/actionCreator';
import Notification from 'src/components/Notification';

const EmpleadoList = () => {
  const dispatch = useDispatch();
  const { alert } = useSelector(state => state.ui);
  const { doRequest } = AxiosAux();  
 

  useEffect(() =>{
    (async () => {
      dispatch(startFetchAll(doRequest));
      dispatch(startFetchAllEstado(doRequest));
    })()
  }, []);
  
  
  return (
  <>
    <Helmet>
      <title>
        Empleados
      </title>
    </Helmet>
    <Box
      sx={{
        backgroundColor: 'background.default',
        minHeight: '100%',
        py: 3
      }}
    >
      <Container maxWidth={false}>
        <EmpleadoListToolbar/>
        <Box sx={{ pt: 3 }}>
         
         <EmpleadoListResults/>
         
        </Box>
      </Container>
      <Notification alert={alert}></Notification>
    </Box>
  </>
)
};

export default EmpleadoList;
