import accions from './actions';

const initState = {
    alert: {
        isOpen: false,
        type: 'success',
        title: '',
        message: ''
    },
    confirmDialog: {
      open: false,
      title: '', 
      subtitle: '',
      onConfirm: () => console.log('confirm'),
      inProgress: false
    }
};

/**
 *
 * @todo impure state mutation/explaination
 */
const UIReducer = (state = initState, action) => {

  const { type, payload } = action;  
  switch (type) {    
    case accions.UI_SET_ALERT:      
      return {
          ...state,
          alert: {
            ...payload
          } 
        };
    case accions.UI_SET_CONFIRM_DIALOG:      
      return {
            ...state,
            confirmDialog: {
              ...state.confirmDialog,
              ...payload
            } 
          };
    default:
      return state;
  }
};
export default UIReducer;
