import accions from './actions';

const initState = {
  categorias: [],
  filterValue: null,
  fetching: true,
  message: "",
  error: ""  
};

/**
 *
 * @todo impure state mutation/explaination
 */
const CategoriasReducer = (state = initState, action) => {

  const { type, payload } = action;  
  switch (type) {
    
    case accions.CATEGORIAS_FETCH:      
      return {
          ...state,
          categorias: payload,
          fetching: false
        };
    case accions.CATEGORIAS_CREATE:     
        return {
          ...state,
          categorias: [...state.categorias, { ...payload }]          
        };
    case accions.CATEGORIAS_UPDATE:     
        return {
          ...state,
          categorias: state.categorias.map(
            cat => ( cat.id === payload.id ? { ...payload } : cat )
          )        
        };
    case accions.CATEGORIAS_SET_ERROR:
  
      return {
          ...state,
          error: payload,
          fetching: false
        };  
    case accions.CATEGORIAS_SET_FILTER:
        return {
          ...state,
          filterValue: payload
        }
    case accions.CATEGORIAS_DISABLE:
      return {
        ...state,
        categorias: state.categorias.map(
          cat => ( cat.id === payload ? { ...cat, estatus: '0' } : cat )
        )  
      }

    case accions.CATEGORIAS_ENABLE:
        return {
          ...state,
          categorias: state.categorias.map(
            cat => ( cat.id === payload ? { ...cat, estatus: '1' } : cat )
          )  
        }
     
    default:
      return state;
  }
};
export default CategoriasReducer;
