import React, { useEffect, useState } from 'react';
import Autocomplete from '@material-ui/core/Autocomplete';
import TextField from '@material-ui/core/TextField';
import propTypes from "prop-types";
/*const useStyles = makeStyles((theme) => ({
  root: {
    width: 500,
    '& > * + *': {
      marginTop: theme.spacing(3),
    },
  },
}));*/

export default function MultiSelect(props) {
  
    const {label, error = null, setFieldValue, setFieldError, options, onBlur, helperText, defaultValues = [] } = props;    
    const [values, setValues] = useState([]);   
    const [filteredOptions, setFilteredOptions] = useState([]);    
    const ids = [];
    useEffect(() =>{          
        if(defaultValues.length > 0){
            setValues(defaultValues.map(value =>(
                { id: value.id, text: value.nombre }
           )));                         
        }           
        filterOptions();   
    }, [defaultValues]);

    const filterOptions = () =>{

        setFilteredOptions(options.filter((x) => {
            const id = values.find(y => y.id === x.id);            
            if(id === undefined){
                return x;
            }
        }));
    }

    useEffect(() =>{
        filterOptions();
    }, [values])
    
                
    return (
        <div>     
            <Autocomplete
                multiple
                id="tags-outlined"
                options={filteredOptions}
                value={values}
                onChange={(event, newValue) => {                                                                                                                                          
                        setValues([...newValue]);
                        setFieldValue('categorias' , [...newValue]);                                       
                        setFieldError("categorias", 'Error');                
                    }
                }
                getOptionLabel={(option) => option.text}
                //{...defaultOptions.length > 0 && { defaultValue: defaultOptions }}                
                filterSelectedOptions
                renderInput={(params) => (
                <TextField
                    {...params}
                    variant="outlined"
                    label={label}
                    placeholder={label}
                    {...helperText && { helperText: helperText }}
                    {...error && { error: error} }
                    onBlur={onBlur}
                />
                )}
            />    
        </div>
    );
}

MultiSelect.propTypes = {    
    label: propTypes.string.isRequired,    
    onBlur: propTypes.func.isRequired,
    options: propTypes.array.isRequired
};
