import accions from './actions';

const initState = {
  capitulos: [],
  filterValue: null,
  fetching: true,
  message: "",
  error: ""  
};

/**
 *
 * @todo impure state mutation/explaination
 */
const CapitulosReducer = (state = initState, action) => {

  const { type, payload } = action;  
  switch (type) {
    
    case accions.CAPITULOS_FETCH:      
      return {
          ...state,
          capitulos: payload,
          fetching: false
        };
    case accions.CAPITULOS_CREATE:     
        return {
          ...state,
          capitulos: [...state.capitulos, { ...payload }]          
        };
    case accions.CAPITULOS_UPDATE:     
        return {
          ...state,
          capitulos: state.capitulos.map(
            cap => ( cap.id === payload.id ? { ...payload } : cap )
          )        
        };
    case accions.CAPITULOS_SET_ERROR:
  
      return {
          ...state,
          error: payload,
          fetching: false
        };  
    case accions.CAPITULOS_SET_FILTER:
        return {
          ...state,
          filterValue: payload
        }
    case accions.CAPITULOS_DISABLE:
      return {
        ...state,
        capitulos: state.capitulos.map(
          cap => ( cap.id === payload ? { ...cap, estatus: '0' } : cap )
        )  
      }

    case accions.CAPITULOS_ENABLE:
        return {
          ...state,
          capitulos: state.capitulos.map(
            cap => ( cap.id === payload ? { ...cap, estatus: '1' } : cap )
          )  
        }
     
    default:
      return state;
  }
};
export default CapitulosReducer;
