import { setConfirmDialog, startSetAlert } from '../ui/actionCreator';
import actions from './actions';

const {
  fetchAll,
  create,
  update,
  setError,
  setChecking,
  setFilter,
  disable,
  enable
} = actions;

export const startFetchAll = (doRequest) => {

    return async (dispatch) => {
        try {
            
            dispatch(setChecking(true));

            const { data } = await doRequest('admin');     
            console.log(data)                                
            dispatch(fetchAll(data.administradores));            
            
        } catch (error) {                  
          if (error.response) {              
            dispatch(setError(error.response.data.message));
          } else {
            dispatch(setError('Error del Servidor'));
          }
        }
    }
};

export const startCreate = (values, doRequest) => {
  
  return async (dispatch) => {
    try {
      dispatch(setError(''));    
      const { data } = await doRequest('admin/signup', {
        ...values
      }, 'POST');

      dispatch(create(data.admin));     

    } catch (error) {          
      throw error;
    }
  }
}

export const startUpdate = (id, values, doRequest) => {
  
  return async (dispatch) => {
    try {
      dispatch(setError(''));    
      const { data } = await doRequest(`admin/${id}`, {
        ...values
      }, 'PUT');

      dispatch(update(data.admin));     

    } catch (error) {           
      throw error;
    }
  }
}

export const startDisable = (id, doRequest) => {
  return async dispatch =>{
    try {
      dispatch(setError(''));    
      await doRequest(`admin/${id}`, {
      }, 'DELETE');

      dispatch(disable(id));
      dispatch(setConfirmDialog({
        open:false,
        inProgress: false
      }));     
      dispatch(startSetAlert({
        type: 'success',
        isOpen: true,
        title: 'Éxito',
        message: 'Administrador Deshabilitado Correctamente'
      })); 

    } catch (error) {
      throw error; 
    }
  }
}

export const startEnable = (id, doRequest) => {
  return async dispatch =>{
    try {
      dispatch(setError(''));    
      await doRequest(`admin/${id}`, {        
      }, 'PATCH');

      dispatch(enable(id));  
      dispatch(setConfirmDialog({
        open:false,
        inProgress: false
      }));     
      dispatch(startSetAlert({
        type: 'success',
        isOpen: true,
        title: 'Éxito',
        message: 'Administrador Habilitado Correctamente'
      })); 
   
    } catch (error) {
      throw error; 
    }
  }
}

export { setError, setFilter };