import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
    Box,
    Typography,
    Grid,
    Button,
    FormGroup,
    FormControlLabel,
    Checkbox
} from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import UploadFile from '@material-ui/icons/UploadFile';
import StyledButton from 'src/components/controls/StyledButton'
import StyledTextField from 'src/components/controls/StyledTextField'
import AxiosAux from 'src/hooks/UseAxios';
import { useDispatch, useSelector } from 'react-redux';
import { startCreate, startUpdate, setError } from 'src/reducers/capitulos/actionCreator';
import { startSetAlert } from 'src/reducers/ui/actionCreator';
import handleRequestError from 'src/helpers/handleReqErrors';
import { startFetchAll } from 'src/reducers/categorias/actionCreator';
import HandleUploadFile from 'src/hooks/UseFile';
import SelectControl from 'src/components/controls/SelectControl';  
import { showFile } from 'src/helpers/showFile';  

const initialValues = {
    id: 0,
    nombre: '',
    descripcion: '',
    tipo: -1,
    path: '',
    duracion: '',
    id: 0,
    idCategoria: -1,
    file: {
        base64: '',
        fileName: ''        
    },
    uploadFile: true
};

const tiposVideo = [
    { id: 1, text: 'Video' },
    { id: 2, text: 'Archivo'}
];

const CapituloForm = ({ title, edit = null }) => {    
    const dispatch = useDispatch();
    const { error } = useSelector(state => state.capitulos);
    const { categorias } = useSelector(state => state.categorias);
    const [categoriasFormatter, setCategoriasFormatter] = useState([]);    
    const { 
        doRequest
    } = AxiosAux();
    const {        
        handleFile,
        ext
    } = HandleUploadFile();

    const validate = () => {
       return Yup.object().shape({
            nombre: Yup.string().min(4, "Longitud Mínima 4 Letras").required('Nombre es Requerido'),
            descripcion: Yup.string().nullable().min(4, "Longitud Mínima 4 Letras").required('Descripción es Requerida'),
            tipo: Yup.number().equals([1, 2], "Seleccione el Tipo del Recurso").required('Tipo del Recurso es Requerido'),            
            duracion: Yup.number().typeError("Ingrese un Número").nullable(true),
            idCategoria: Yup.number().positive("Seleccione una Categoría").required('Categoría es Requerida'),
            file: Yup.object().shape({              
                fileName: Yup.string().required("Seleccione un Archivo / Video")                
            })
        });
    }

    const handleSubmit = async (values, setFieldError) => {        
        try {                                  
            let message = '';               
            if(edit){                
                await dispatch(startUpdate(edit.id, values.uploadFile ? {...values} : { ...values, base64: null }, doRequest));                                
                message = 'Capítulo Actualizado Correctamente';
            }else{                            
                await dispatch(startCreate(values.uploadFile ? {...values} : { ...values, base64: null }, doRequest)); 
                message = 'Capítulo Registrado Correctamente';                   
            }                                         
            dispatch(startSetAlert({
                isOpen: true,
                title: 'Éxito',
                message: message
            }));
        } catch (err) {     
            console.log(err)                   
            if (err?.response?.status !== 500) {
                const stringError = handleRequestError(err, setFieldError);
                dispatch(setError(stringError));
            }            
            dispatch(startSetAlert({
                type: 'error',
                isOpen: true,
                title: 'Error',
                message: "Error del Servidor, Intentelo de Nuevo"
                }));                  
        }
    }  

    useEffect(() => {
        setCategoriasFormatter(categorias.map((cat) => (
            { id: cat.id, text: `${cat.nombre}` }
        )));
    }, [categorias]);
    
    useEffect(() => {
        dispatch(setError(''));    
        //get Categorias
        dispatch(startFetchAll(doRequest));            
    }, []);

    return (        
        <Formik
            initialValues={edit ? { ...edit, tipo: edit.tipo === 'video' ? 1 : 2} : initialValues}   
            validationSchema={
                validate()
            }
            validateOnChange={true}    
            onSubmit={(values, { setFieldError }) => handleSubmit(values, setFieldError)}
        >
            {({
                errors,                
                handleBlur,
                handleChange,
                handleSubmit,
                isSubmitting,
                touched,
                values,
                setFieldValue
            }) => (
                <form onSubmit={handleSubmit}>
                    <Box sx={{ mb: 3 }}>
                        <Typography
                            color="textPrimary"
                            variant="h2"
                        >
                            {title}
                        </Typography>
                    </Box>
                    <Grid container spacing={1}>
                        <Grid item xs={6}>                         
                            <StyledTextField
                                error={Boolean(touched.nombre && errors.nombre)}
                                helperText={touched.nombre && errors.nombre}
                                label="Nombre"                                
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="nombre"
                                value={values.nombre}
                            />
                            <StyledTextField
                                error={Boolean(touched.descripcion && errors.descripcion)}
                                helperText={touched.descripcion && errors.descripcion}
                                label="Descripción"                             
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="descripcion"
                                value={values.descripcion}
                            />
                            <SelectControl
                                    label='Tipo'
                                    name='tipo'
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    options={tiposVideo}
                                    value={values.tipo}
                                    error={Boolean(touched.tipo && errors.tipo)}
                                    helperText={touched.tipo && errors.tipo}
                            />
                            
                            {
                                values.tipo === 'video' &&
                                <StyledTextField
                                    error={Boolean(touched.duracion && errors.duracion)}
                                    helperText={touched.duracion && errors.duracion}
                                    label="Duración"                             
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    name="duracion"
                                    value={values.duracion}
                                />                                
                            }                                    
                          
                        </Grid>
                        <Grid item xs={6}>
                            <SelectControl
                                    label='Categoría'
                                    name='idCategoria'
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    options={categoriasFormatter}
                                    value={values.idCategoria}
                                    error={Boolean(touched.idCategoria && errors.idCategoria)}
                                    helperText={touched.idCategoria && errors.idCategoria}
                            />
                                                    
                        </Grid>
                        <Grid item xs={12}>   
                            {                            
                                <StyledTextField
                                    error={Boolean(touched.file?.fileName && errors.file?.fileName)}
                                    helperText={touched.file?.fileName && errors.file?.fileName}
                                    label={edit ? 'Archivo' : 'Archivo Seleccionado'}                             
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    name="file.fileName"
                                    fullWidth={false}
                                    disabled={true}
                                    value={values.file.fileName}
                                /> 
                            }  
                        </Grid>
                        <Grid item xs={12}>
                            <Button
                                color='info'                                                                      
                                startIcon={<UploadFile></UploadFile>}                                
                                variant={'outlined'}
                                onClick={() => document.getElementById("fileSelector").click()}  
                                onChange={(e) => { handleFile(e, setFieldValue) }}                         
                                >
                                <input
                                    type="file"
                                    style={{ display: "none" }}                                        
                                    id="fileSelector"                                                                                
                                />
                                Seleccionar Archivo
                            </Button>                            
                        </Grid>
                        <Grid item xs={12}>                            
                            <FormGroup row>
                                <FormControlLabel
                                    control={<Checkbox checked={values.uploadFile} onChange={handleChange} name="uploadFile" />}
                                    label="Subir Archivo"
                                />
                            </FormGroup>
                            <Grid item xs={6}>
                                <Typography variant="button">Si desmarcas está casilla, no se subirá el archivo pero se creará el registro y ruta para subirlo por FTP</Typography>
                            </Grid>
                            
                        </Grid>                            
                        <Grid item xs={12}>                       
                            {                                
                               values.path !== '' &&                               
                               showFile({path: values.path, tipo: values.tipo}, ext)
                            }                            
                        </Grid>
                    </Grid>
                    <Box sx={{ py: 2 }}>
                        <StyledButton
                            color='primary'
                            type='submit'
                            startIcon={<SaveIcon></SaveIcon>}
                            disabled={isSubmitting}
                            variant={'contained'}
                        >
                            Registrar
                        </StyledButton>
                        <Typography color="error" align="center" style={{ marginTop: '10px' }}>
                            {error}
                        </Typography>                        
                    </Box>
                </form>
            )}
        </Formik>
    )
}

CapituloForm.propTypes = {
    title: PropTypes.string.isRequired,    
};

export default CapituloForm;
