import { useState } from "react";
import { useDispatch } from "react-redux";
import { encodeImageFileAsURL } from "src/helpers/base64";
import { startSetAlert } from "src/reducers/ui/actionCreator";


export default function HandleUploadFile(){
    const dispatch = useDispatch();
    const [ext, setExt] = useState(null);
    const handleFile = async(e, setValue) => {        
        
        try{
            if(e.target.files.length > 0){
                const file = e.target.files[0];
                setValue('file.fileName', file.name, true);        
                const base64 = await encodeImageFileAsURL(file);
                setValue('file.base64', base64);       
                setValue('path', base64);     
                setExt(file.name.split('.').pop());            
            }
           
        }catch(error ){
            dispatch(startSetAlert({
                isOpen: true,
                type: 'error',
                title: 'error',
                message: 'Error al Seleccionar el Archivo, Intentelo de Nuevo'
            }));
        }                 
    };  

    return {
        handleFile,
        ext        
    }
}

