import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import * as Yup from 'yup';
import { Formik } from 'formik';
import {
    Box,
    Typography,
    Grid    
} from '@material-ui/core';
import SaveIcon from '@material-ui/icons/Save';
import StyledButton from 'src/components/controls/StyledButton'
import StyledTextField from 'src/components/controls/StyledTextField'
import AxiosAux from 'src/hooks/UseAxios';
import { useDispatch, useSelector } from 'react-redux';
import { startCreate, startUpdate, setError } from 'src/reducers/empleados/actionCreator';
import { startSetAlert } from 'src/reducers/ui/actionCreator';
import handleRequestError from 'src/helpers/handleReqErrors';
import SelectControl from 'src/components/controls/SelectControl';
import { startFetchAll } from 'src/reducers/departamentos/actionCreator';

const initialValues = {
    persona:{
        nombre: '',
        primerAp: '',
        segundoAp: '',
        telefono: '',    
    },    
    usuario: {
        password: '',
        email: '',
        confirmPassword: '',
        nivelAcceso: 1
    },    
    noInterno: '',
    id: 0,
    ciudad: '',
    colonia: '',
    numExt: '',
    numInt: '',
    cp: '',
    idEstado: -1,
    idDepartamento: -1,
    calle: ''

};

const phoneRegex = /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/;

const AdministradorForm = ({ title, edit = null }) => {    
    const dispatch = useDispatch();
    const { error, estados } = useSelector(state => state.empleados);
    const { departamentos } = useSelector(state => state.departamentos);
    const [estadosFormatter, setEstadosFormatter] = useState([{id: -1, text:''}]);
    const [optDepartamentos, setOptDepartamentos] = useState([])
    const { 
        doRequest
    } = AxiosAux();

    const validate = () => {
       return Yup.object().shape({
            persona: Yup.object().shape({
                nombre: Yup.string().max(255).required('Nombre es Requerido'),
                primerAp: Yup.string().max(255).required('Primer Apellido es Requerido'),
                telefono: Yup.string().matches(phoneRegex, 'Número de Teléfono no Válido').min(10, 'Longitud mínima de 10 números').required('Teléfono es Requerido'),            
            }),                             
            usuario: Yup.object().shape({
                email: edit ? Yup.string().email('Debe ser un Correo Electrónico Válido').max(255) 
                            : Yup.string().email('Debe ser un Correo Electrónico Válido').max(255).required('El Correo Electrónico es Requerido'),
                password: edit ?  Yup.string().length(6, 'Longitud mínima: 6 carácteres')
                                : Yup.string().length(6, 'Longitud mínima: 6 carácteres').required('Contraseña Requerida'),
                confirmPassword: edit ? Yup.string().length(6, 'Logitud mínima: 6 caráteres')
                                                .oneOf([Yup.ref('password'), null], 'Las Contraseñas no Coinciden')
                                        : Yup.string().length(6, 'Logitud mínima: 6 caráteres')
                                                    .oneOf([Yup.ref('password'), null], 'Las Contraseñas no Coinciden')
                                                    .required('Confirmación de Contraseña Requerida')
            })

        });
    }

    const handleSubmit = async (values, setFieldError) => {        
        try {                                 
            let message = '';
            if(edit){                
                await dispatch(startUpdate(edit.id, values, doRequest));                                
                message = 'Empleado Actualizado Correctamente';
            }else{                            
                await dispatch(startCreate(values, doRequest)); 
                message = 'Empleado Registrado Correctamente';                   
            }                                         
            dispatch(startSetAlert({
                isOpen: true,
                title: 'Éxito',
                message: message
            }));
        } catch (err) {  
            if (err?.response?.status !== 500) {
                const stringError = handleRequestError(err, setFieldError);
                dispatch(setError(stringError));
            }
            dispatch(startSetAlert({
                type: 'error',
                isOpen: true,
                title: 'Error',
                message: "Error del Servidor, Intentelo de Nuevo"
            }));                                                 
        }
    }  

    const formatDataEstados = () =>{
        const ests = estados.map(estado => (
            {id: estado.id, text: estado.nombre}
        ));
        setEstadosFormatter(ests);
    }

    const formatDataDepartamentos = () =>{
        const deps = departamentos.map(depar => (
            {id: depar.id, text: depar.nombre}
        ));
        setOptDepartamentos(deps);
    }
    
    useEffect(() => {
        dispatch(setError(''));    
        dispatch(startFetchAll(doRequest));    
    }, []);

    useEffect(()=>{        
        formatDataEstados();
    }, [estados]);

    useEffect(()=>{        
        formatDataDepartamentos();
    }, [departamentos]);

    return (        
        <Formik
            initialValues={edit ? edit : initialValues}   
            validationSchema={
                validate()
            }
            validateOnChange={true}    
            onSubmit={(values, { setFieldError }) => handleSubmit(values, setFieldError)}
        >
            {({
                errors,                
                handleBlur,
                handleChange,
                handleSubmit,
                isSubmitting,
                touched,
                values
            }) => (
                <form onSubmit={handleSubmit}>
                    <Box sx={{ mb: 3 }}>
                        <Typography
                            color="textPrimary"
                            variant="h2"
                        >
                            {title}
                        </Typography>
                    </Box>
                    <Grid container spacing={1}>
                        <Grid item xs={6}>                         
                            <StyledTextField
                                error={Boolean(touched.persona?.nombre && errors.persona?.nombre)}
                                helperText={touched.persona?.nombre && errors.persona?.nombre}
                                label="Nombre"
                                disabled={Boolean(edit)}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="persona.nombre"
                                value={values.persona?.nombre}
                            />
                            <StyledTextField
                                error={Boolean(touched.persona?.primerAp && errors.persona?.primerAp)}
                                helperText={touched.persona?.primerAp && errors.persona?.primerAp}
                                label="Primer Apellido"
                                disabled={Boolean(edit)}
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="persona.primerAp"
                                value={values.persona?.primerAp}
                            />
                            <StyledTextField
                                error={Boolean(touched.persona?.segundoAp && errors.persona?.segundoAp)}
                                helperText={touched.persona?.segundoAp && errors.persona?.segundoAp}
                                label="Segundo Apellido"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="persona.segundoAp"
                                value={values.persona?.segundoAp}
                            />
                            <StyledTextField
                                error={Boolean(touched.persona?.telefono && errors.persona?.telefono)}
                                helperText={touched.persona?.telefono && errors.persona?.telefono}
                                label="Teléfono"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="persona.telefono"
                                value={values.persona?.telefono}
                            />
                            <StyledTextField
                                error={Boolean(touched.noInterno && errors.noInterno)}
                                helperText={touched.noInterno && errors.noInterno}
                                label="Número Interno"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="noInterno"
                                value={values.noInterno}
                            />       
                             <StyledTextField
                                error={Boolean(touched.calle && errors.calle)}
                                helperText={touched.calle && errors.calle}
                                label="Calle"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="calle"
                                value={values.calle}
                            />                     
                        </Grid>
                        <Grid item xs={6}>
                           
                            <StyledTextField
                                error={Boolean(touched.colonia && errors.colonia)}
                                helperText={touched.colonia && errors.colonia}
                                label="Colonia"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="colonia"
                                value={values.colonia}
                            />
                            <StyledTextField
                                error={Boolean(touched.numExt && errors.numExt)}
                                helperText={touched.numExt && errors.numExt}
                                label="Número Exterior"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="numExt"
                                value={values.numExt}
                            />
                            <StyledTextField
                                error={Boolean(touched.numInt && errors.numInt)}
                                helperText={touched.numInt && errors.numInt}
                                label="Número Interior"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="numInt"
                                value={values.numInt}
                            />
                             <StyledTextField
                                error={Boolean(touched.cp && errors.cp)}
                                helperText={touched.cp && errors.cp}
                                label="Código Postal"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="cp"
                                value={values.cp}
                            />
                            <StyledTextField
                                error={Boolean(touched.ciudad && errors.ciudad)}
                                helperText={touched.ciudad && errors.ciudad}
                                label="Ciudad"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="ciudad"
                                value={values.ciudad}
                            />
                            <SelectControl
                                    label='Estado'
                                    name='idEstado'
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    options={estadosFormatter}
                                    value={values.idEstado}
                                    error={Boolean(touched.idEstado && errors.idEstado)}
                                    helperText={touched.idEstado && errors.idEstado}
                                >                                    
                            </SelectControl>
                        </Grid>
                        <Grid item xs={6}>
                           
                            <StyledTextField
                                error={Boolean(touched.usuario?.email && errors.usuario?.email)}
                                helperText={touched.usuario?.email && errors.usuario?.email}
                                label="Correo Electrónico"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="usuario.email"
                                value={values.usuario?.email}
                            />
                            <StyledTextField
                                error={Boolean(touched.usuario?.password && errors.usuario?.password)}
                                helperText={touched.usuario?.password && errors.usuario?.password}
                                label="Contraseña"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="usuario.password"
                                value={values.usuario?.password}
                                type='password'
                            />
                            <StyledTextField
                                error={Boolean(touched.usuario?.confirmPassword && errors.usuario?.confirmPassword)}
                                helperText={touched.usuario?.confirmPassword && errors.usuario?.confirmPassword}
                                label="Confirmar Contraseña"
                                onBlur={handleBlur}
                                onChange={handleChange}
                                name="usuario.confirmPassword"
                                value={values.usuario?.confirmPassword}
                                type='password'
                            />                           
                        </Grid>
                        <Grid item xs={6}>
                            <SelectControl
                                    label='Departamento'
                                    name='idDepartamento'
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    options={optDepartamentos}
                                    value={values.idDepartamento}
                                    error={Boolean(touched.idDepartamento && errors.idDepartamento)}
                                    helperText={touched.idDepartamento && errors.idDepartamento}
                                >                                    
                            </SelectControl>
                        </Grid>
                    </Grid>
                    <Box sx={{ py: 2 }}>
                        <StyledButton
                            color='primary'
                            type='submit'
                            startIcon={<SaveIcon></SaveIcon>}
                            disabled={isSubmitting}
                            variant={'contained'}
                        >
                            Registrar
                        </StyledButton>
                        <Typography color="error" align="center" style={{ marginTop: '10px' }}>
                            {error}
                        </Typography>                        
                    </Box>
                </form>
            )}
        </Formik>
    )
}

AdministradorForm.propTypes = {
    title: PropTypes.string.isRequired,    
};

export default AdministradorForm;
