import { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  CircularProgress
} from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import StyledButton from 'src/components/controls/StyledButton';
import UsePagination from "src/hooks/UsePagination";
import StyledDialog from '../Dialog';
import CategoriaForm from './CategoriaForm';
import AxiosAux from 'src/hooks/UseAxios';
import ConfirmDialog from '../ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { setConfirmDialog, startSetAlert } from 'src/reducers/ui/actionCreator';
import { setError, startDisable, startEnable } from "src/reducers/categorias/actionCreator";
import { dateFormater } from 'src/helpers/formatters';
import handleRequestError from 'src/helpers/handleReqErrors';

const useStyles = makeStyles(() => ({
  child:{
    
  },
  root:{
    textAlign: 'center'
  }
}));

const CategoriaListResults = ({...rest }) => {
  const dispatch = useDispatch();
  const { categorias, filterValue, fetching } = useSelector(state => state.categorias);      
  const [open, setOpen] = useState(false);
  const [filteredCategorias, setFilteredCategorias] = useState([]);
  const [childrenDialog, setChildrenDialog] = useState({ title: "", children: null });
  const classes = useStyles();

  const {
    handleLimitChange,
    handlePageChange,
    limit,
    page
  } = UsePagination();

  const {
    doRequest
  } = AxiosAux();

  const filter = (value) => {
    if (value !== "") {
      const filtered = categorias.filter(
        x =>
          x.id === value ||
          x.nombre.toLowerCase().includes(value) ||
          x.descripcion.toLowerCase().includes(value) 
      );
      setFilteredCategorias(filtered);
    } else {
      setFilteredCategorias(categorias);
    }

  }

  const openDialog = (categoria) => {
    const title = 'Actualizar Categoría';
    //Se formatea la estructura del objeto del categoria
      
    setChildrenDialog({
      title: title,
      children: <CategoriaForm setOpen={setOpen} title={title} edit={categoria} ></CategoriaForm>
    });

  }

  const startDeleteOrEnable = (id, estatus, nombre) => {
    dispatch(setConfirmDialog({
      open: true,
      title: estatus === '1' ? `Desea Deshabilitar la Categoría ${nombre}?` : `Desea Habilitar la Categoría ${nombre}?`,
      subtitle: "Está Acción puede ser Revertida",      
      onConfirm: () => { handleDeleteOrEnable(id, estatus); dispatch(setConfirmDialog({inProgress: true})) }
    }));
  }

  const handleDeleteOrEnable = async (id, estatus) => {
    try {
      if (estatus === '1') {
        await dispatch(startDisable(id, doRequest));
      } else {
        await dispatch(startEnable(id, doRequest));
      }               

    } catch (err) {      
      if (err?.response?.status !== 500) {
        const stringError = handleRequestError(err);
        dispatch(setError(stringError));
      }
      dispatch(startSetAlert({
        type: 'error',
        isOpen: true,
        title: 'Error',
        message: "Error del Servidor, Intentelo de Nuevo"
      }));       
      dispatch(setConfirmDialog({inProgress: false, open: false}));
    }
  }

  useEffect(() => {
    if (childrenDialog.children !== null) {
      setOpen(true);
    }
  }, [childrenDialog]);

  useEffect(() => {
    if (categorias) {
      setOpen(false);
      setFilteredCategorias(categorias)
    }
  }, [categorias]);

  useEffect(() => {
    if (filterValue !== null) {
      filter(filterValue);
    }
  }, [filterValue]);



  return (
    <Card {...rest}>
     
      <PerfectScrollbar>
        <Box sx={{ minWidth: 1050 }}>
         
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Clave categoria
                </TableCell>
                <TableCell>
                  Nombre
                </TableCell>
                <TableCell>
                  Descripción
                </TableCell>
                <TableCell>
                  Fecha de Registro
                </TableCell>             
                <TableCell>
                  Estatus
                </TableCell>
                <TableCell>
                  Acciones
                </TableCell>
              </TableRow>
            </TableHead>
            
            <TableBody>      
               
              {
                filteredCategorias.slice(page * limit, (page + 1) * limit).map(categoria => (
                  <TableRow key={categoria.id}>
                    <TableCell>{categoria.id}</TableCell>
                    <TableCell>{categoria.nombre}</TableCell>
                    <TableCell>{categoria.descripcion}</TableCell>
                    <TableCell>{dateFormater(categoria.createdAt)}</TableCell>                                      
                    <TableCell>{categoria.estatus === '1' ? <Typography variant='button' color="green">Activa</Typography> 
                                                          : <Typography variant='button' color="red">No Activa</Typography>}</TableCell>
                    <TableCell>
                      <StyledButton color='primary' onClick={() => openDialog(categoria)} endIcon={<CreateIcon></CreateIcon>}> Editar </StyledButton>
                      {
                        categoria.estatus === '1' ?
                          <StyledButton color='error' onClick={() => startDeleteOrEnable(categoria.id, categoria.estatus, categoria.nombre)} endIcon={<DeleteIcon></DeleteIcon>}> Deshabilitar </StyledButton>
                          :
                          <StyledButton color='info' onClick={() => startDeleteOrEnable(categoria.id, categoria.estatus, categoria.nombre)} endIcon={<ArrowUpward></ArrowUpward>}> Habilitar </StyledButton>
                      }

                    </TableCell>
                  </TableRow>
                ))
              }
            </TableBody>
            
          </Table>
          {
               fetching && 
                <div className={classes.root}>
                  <CircularProgress color="primary"/>    
                </div>                                
            }  
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredCategorias.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
      <StyledDialog
        title={childrenDialog.title}
        open={open}
        setOpen={setOpen}
      >
        {childrenDialog.children}
      </StyledDialog>
      <ConfirmDialog>
      </ConfirmDialog>
    </Card>
  );
};

export default CategoriaListResults;
