import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, IconButton } from '@material-ui/core'
import { makeStyles } from '@material-ui/styles';
import React from 'react'
import NotListedLocationIcon from '@material-ui/icons/NotListedLocation';
import theme from "src/theme/index";
import { useDispatch, useSelector } from 'react-redux';
import { setConfirmDialog } from 'src/reducers/ui/actionCreator';

const useStyles = makeStyles(() => ({
    dialog: {
        padding: theme.spacing(2),
        position: 'absolute',
        top: theme.spacing(5)
    },
    dialogTitle: {
        textAlign: 'center'
    },
    dialogContent: {
        textAlign: 'center'
    },
    dialogAction: {
        justifyContent: 'center'
    },
    titleIcon: {
        backgroundColor: "#fff",
        color: theme.palette.secondary.main,
        '&:hover': {
            backgroundColor: "theme.palette.secondary.light",
            cursor: 'default'
        },
        '& .MuiSvgIcon-root': {
            fontSize: '8rem',
        }
    }
}))

export default function ConfirmDialog(props) {
    const dispatch = useDispatch();
    const { confirmDialog } = useSelector(state => state.ui);
    const {setTextOk = null /*, ...others*/} = props;
    const classes = useStyles()

    const hide = () =>{
        dispatch(setConfirmDialog({open:false, inProgress: false}));
    }

    return (
        <Dialog open={confirmDialog.open} classes={{paper: classes.dialog}}>
            <DialogTitle className={classes.dialogTitle}>
                <IconButton disableRipple className={classes.titleIcon}>
                    <NotListedLocationIcon />
                </IconButton>
            </DialogTitle>
            <DialogContent className={classes.dialogContent}>
                <Typography variant="h6">
                    {confirmDialog.title}
                </Typography>
                <Typography variant="subtitle2">
                    
                    {confirmDialog.subtitle}
                </Typography>
                
                
            </DialogContent>
            <DialogActions className={classes.dialogAction}>
                <Button
                    
                    variant="contained"
                    color="inherit"
                    size="medium"       
                    onClick={() => hide()}                                            
                   >
                       {
                        setTextOk !== null ? "Cancelar" : "No"   
                       }
                    
                </Button>
                <Button
                    disabled={confirmDialog.inProgress}
                    variant="contained"
                    color="primary"
                    size="medium"                                                    
                    onClick={confirmDialog.onConfirm}
                >
                    {
                        setTextOk !== null ? setTextOk : "Si"
                    }
                </Button>
            </DialogActions>
        </Dialog>
    )
}
