import { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  CircularProgress
} from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import People from '@material-ui/icons/People';
import StyledButton from 'src/components/controls/StyledButton';
import UsePagination from "src/hooks/UsePagination";
import StyledDialog from '../Dialog';
import DepartamentoForm from './DepartamentoForm';
import AxiosAux from 'src/hooks/UseAxios';
import ConfirmDialog from '../ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { setConfirmDialog, startSetAlert } from 'src/reducers/ui/actionCreator';
import { setError, startDisable, startEnable } from "src/reducers/departamentos/actionCreator";
import { dateFormater } from 'src/helpers/formatters';
import handleRequestError from 'src/helpers/handleReqErrors';
import EmpleadoTable from './EmpleadoTable';

const useStyles = makeStyles(() => ({
  child:{
    
  },
  root:{
    textAlign: 'center'
  }
}));

const DepartamentoListResults = ({...rest }) => {
  const dispatch = useDispatch();
  const { departamentos, filterValue, fetching } = useSelector(state => state.departamentos);      
  const [open, setOpen] = useState(false);
  const [filteredDepartamentos, setFilteredDepartamentos] = useState([]);
  const [childrenDialog, setChildrenDialog] = useState({ title: "", children: null });
  const classes = useStyles();

  const {
    handleLimitChange,
    handlePageChange,
    limit,
    page
  } = UsePagination();

  const {
    doRequest
  } = AxiosAux();

  const filter = (value) => {
    if (value !== "") {
      const filtered = departamentos.filter(
        x =>
          x.id === value ||
          x.nombre.toLowerCase().includes(value) ||
          x.descripcion.toLowerCase().includes(value) 
      );
      setFilteredDepartamentos(filtered);
    } else {
      setFilteredDepartamentos(departamentos);
    }

  }

  const openDialog = (departamento) => {
    const title = 'Actualizar Departamento';
    //Se formatea la estructura del objeto del departamento      
    setChildrenDialog({
      title: title,
      children: <DepartamentoForm setOpen={setOpen} title={title} edit={departamento} ></DepartamentoForm>
    });
  }

  const showEmpleados = (id) =>{
    setChildrenDialog({
      title: 'Empleados Asignados',
      children: <EmpleadoTable id={id}></EmpleadoTable>
    });
  }

  const startDeleteOrEnable = (id, estatus, nombre) => {
    dispatch(setConfirmDialog({
      open: true,
      title: estatus === '1' ? `Desea Deshabilitar el Departamento ${nombre}?` : `Desea Habilitar el Departamento ${nombre}?`,
      subtitle: "Está Acción puede ser Revertida",      
      onConfirm: () => { handleDeleteOrEnable(id, estatus); dispatch(setConfirmDialog({inProgress: true})) }
    }));
  }

  const handleDeleteOrEnable = async (id, estatus) => {
    try {
      if (estatus === '1') {
        await dispatch(startDisable(id, doRequest));
      } else {
        await dispatch(startEnable(id, doRequest));
      }               

    } catch (err) {      
      if (err?.response?.status !== 500) {
        const stringError = handleRequestError(err);
        dispatch(setError(stringError));
      }
      dispatch(startSetAlert({
        type: 'error',
        isOpen: true,
        title: 'Error',
        message: "Error del Servidor, Intentelo de Nuevo"
      }));       
      dispatch(setConfirmDialog({inProgress: false, open: false}));
    }
  }

  useEffect(() => {
    if (childrenDialog.children !== null) {
      setOpen(true);
    }
  }, [childrenDialog]);

  useEffect(() => {
    if (departamentos) {
      setOpen(false);
      setFilteredDepartamentos(departamentos)
    }
  }, [departamentos]);

  useEffect(() => {
    if (filterValue !== null) {
      filter(filterValue);
    }
  }, [filterValue]);



  return (
    <Card {...rest}>
     
      <PerfectScrollbar>
        <Box sx={{ minWidth: 1050 }}>
         
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Clave departamento
                </TableCell>
                <TableCell>
                  Nombre
                </TableCell>
                <TableCell>
                  Descripción
                </TableCell>
                <TableCell>
                  Ver Empleados
                </TableCell>
                <TableCell>
                  Fecha de Registro
                </TableCell>             
                <TableCell>
                  Estatus
                </TableCell>
                <TableCell>
                  Acciones
                </TableCell>
              </TableRow>
            </TableHead>
            
            <TableBody>      
               
              {
                filteredDepartamentos.slice(page * limit, (page + 1) * limit).map(departamento => (
                  <TableRow key={departamento.id}>
                    <TableCell>{departamento.id}</TableCell>
                    <TableCell>{departamento.nombre}</TableCell>
                    <TableCell>{departamento.descripcion}</TableCell>
                    <TableCell>
                      <StyledButton color='info' onClick={() => showEmpleados(departamento.id)} endIcon={<People></People>}> 
                        Empleados 
                      </StyledButton>
                    </TableCell>
                    <TableCell>{dateFormater(departamento.createdAt)}</TableCell>                                      
                    <TableCell>{departamento.estatus === '1' ? <Typography variant='button' color="green">Activa</Typography> 
                                                          : <Typography variant='button' color="red">No Activa</Typography>}</TableCell>
                    <TableCell>
                      <StyledButton color='primary' onClick={() => openDialog(departamento)} endIcon={<CreateIcon></CreateIcon>}> Editar </StyledButton>
                      {
                        departamento.estatus === '1' ?
                          <StyledButton color='error' onClick={() => startDeleteOrEnable(departamento.id, departamento.estatus, departamento.nombre)} endIcon={<DeleteIcon></DeleteIcon>}> Deshabilitar </StyledButton>
                          :
                          <StyledButton color='info' onClick={() => startDeleteOrEnable(departamento.id, departamento.estatus, departamento.nombre)} endIcon={<ArrowUpward></ArrowUpward>}> Habilitar </StyledButton>
                      }

                    </TableCell>
                  </TableRow>
                ))
              }
            </TableBody>
            
          </Table>
          {
               fetching && 
                <div className={classes.root}>
                  <CircularProgress color="primary"/>    
                </div>                                
            }  
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredDepartamentos.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
      <StyledDialog
        title={childrenDialog.title}
        open={open}
        setOpen={setOpen}
      >
        {childrenDialog.children}
      </StyledDialog>
      <ConfirmDialog>
      </ConfirmDialog>
    </Card>
  );
};

export default DepartamentoListResults;
