import { combineReducers } from 'redux';
import authReducer from './auth/reducers';
import administradoresReducer from './administradores/reducers';
import uiReducer from './ui/reducers';
import empleadosReducer from './empleados/reducers';
import departamentosReducer from './departamentos/reducers';
import categoriasReducer from './categorias/reducers';
import capitulosReducer from './capitulos/reducers';


const rootReducers = combineReducers({
  auth: authReducer,
  administradores: administradoresReducer,
  empleados: empleadosReducer,
  ui: uiReducer,
  departamentos: departamentosReducer,
  categorias: categoriasReducer,
  capitulos: capitulosReducer
});

export default rootReducers;
