import { Navigate } from 'react-router-dom';
import DashboardLayout from 'src/components/DashboardLayout';
import MainLayout from 'src/components/MainLayout';
import Account from 'src/pages/Account';
import AdministradorList from 'src/pages/AdministradorList';
import EmpleadoList from 'src/pages/EmpleadoList';
import Dashboard from 'src/pages/Dashboard';
import Login from 'src/pages/Login';
import NotFound from 'src/pages/NotFound';
import CategoriaList from './pages/CategoriaList';
import CapituloList from './pages/CapituloList';
import DepartamentoList from './pages/DepartamentoList';

const publicRoutes = [
  {
    path: '/',
    element: <MainLayout />,
    children: [
      { path: 'login', element: <Login /> },      
      { path: '404', element: <NotFound /> },
      { path: '/', element: <Navigate to="/login" /> },
      { path: '*', element: <Navigate to="/login" /> }
    ]
  }
];

const adminRoutes = [
  {
    path: 'app',
    element: <DashboardLayout />,
    children: [
      { path: 'perfil', element: <Account /> },     
      { path: 'empleados', element: <EmpleadoList /> },
      { path: 'categorias', element: <CategoriaList /> },
      { path: 'capitulos', element: <CapituloList /> },
      { path: 'departamentos', element: <DepartamentoList /> },
      { path: '*', element: <Navigate to="/404" /> }
    ]
  },
  {
    path: '*', element: <Navigate to="/app"></Navigate>    
  }
];

const superAdminRoutes = [
  {
    path: 'app',
    element: <DashboardLayout />,
    children: [
    
      { path: 'administradores', element: <AdministradorList /> },
      ...adminRoutes[0].children
    ]
  },
  {
    path: '*', element: <Navigate to="/app"></Navigate>    
  }
];

export {
  superAdminRoutes,
  adminRoutes,
  publicRoutes
};
