import {
  Box,
  Button,
  Card,
  CardContent,
  TextField,
  InputAdornment,
  SvgIcon
} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import CreateIcon from '@material-ui/icons/Create';
import { useEffect, useState } from 'react';
import { Search as SearchIcon } from 'react-feather';
import StyledDialog from '../Dialog';
import AdministradorForm from './AdministradorForm';
import { setFilter } from 'src/reducers/administradores/actionCreator';

const AdministradorListToolbar = ( ) => {

  const { administradores } = useSelector(state => state.administradores);
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);

  useEffect(() =>{
    if(administradores){
      setOpen(false);
    }
  },[administradores]);

  const filter = (e) => {
    dispatch(setFilter(e.target.value.toLowerCase()));
  }

  return (
    <Box>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-end'
        }}
      >
        <Button
          color="primary"
          variant="contained"
          startIcon={<CreateIcon></CreateIcon>}
          onClick={() => setOpen(true)}
        >
          Registrar Administrador
        </Button>
      </Box>
      <Box sx={{ mt: 3 }}>
        <Card>
          <CardContent>
            <Box sx={{ maxWidth: 500 }}>
              <TextField
                fullWidth
                onChange={filter}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SvgIcon
                        fontSize="small"
                        color="action"
                      >
                        <SearchIcon />
                      </SvgIcon>
                    </InputAdornment>
                  )
                }}
                placeholder="Buscar Administrador"
                variant="outlined"
              />
            </Box>
          </CardContent>
        </Card>
      </Box>
      <StyledDialog
        open={open}
        setOpen={setOpen}
        title={"Registrar Nuevo Administrador"}
      >
        <AdministradorForm
          title={"Registrar"}                      
        >          
        </AdministradorForm>       
      </StyledDialog>      
    </Box>
  );
}

export default AdministradorListToolbar;
