const accions = {        
    ADMINS_FETCH: '[admins]fetch all admins',
    ADMINS_UPDATE: '[admins] update admin',
    ADMINS_CREATE: '[admins] create admin',
    ADMINS_SET_ERROR: '[admins] error',        
    ADMINS_SET_CHECKING: '[admins] fetching...',
    ADMINS_SET_FILTER: '[admins] set value to filter admins',
    ADMINS_DISABLE: '[admins] disable admin',
    ADMINS_ENABLE: '[admins] enable admin',
        
    fetchAll: (admins) => {
        return{
            type: accions.ADMINS_FETCH,
            payload: admins
        }
    },

    create: (admin) => {
        return{
            type: accions.ADMINS_CREATE,
            payload: admin
        }
    },
    update: (admin) => {
        return{
            type: accions.ADMINS_UPDATE,
            payload: admin
        }
    },
    
    setChecking: (value) => {
        return {
            type: accions.ADMINS_SET_CHECKING,
            payload: value
        }        
    },    
    
    setError: (error) =>{
        return {
            type: accions.ADMINS_SET_ERROR,
            payload: error
        }
    },
    setFilter: (value) =>{
        return {
            type: accions.ADMINS_SET_FILTER,
            payload: value
        }
    },
    disable: (value) =>{
        return {
            type: accions.ADMINS_DISABLE,
            payload: value
        }
    },
    enable: (value) =>{
        return {
            type: accions.ADMINS_ENABLE,
            payload: value
        }
    }
    
}

export default accions;