import accions from './actions';

const initState = {
  departamentos: [],  
  empleados: [],
  filterValue: null,
  fetching: true,
  message: "",
  error: "",
  deparCategorias: []  
};

/**
 *
 * @todo impure state mutation/explaination
 */
const DepartamentosReducer = (state = initState, action) => {

  const { type, payload } = action;  
  switch (type) {
    
    case accions.DEPARTAMENTOS_FETCH:      
      return {
          ...state,
          departamentos: payload,
          fetching: false
        };
    case accions.DEPARTAMENTOS_EMPLEADOS_ASIGNADOS:      
      return {
          ...state,
          empleados: payload,
          fetching: false
        };
    case accions.DEPARTAMENTOS_FETCH_CATEGORIA:      
        return {
            ...state,
            deparCategorias: payload,
            fetching: false
          };
    case accions.DEPARTAMENTOS_CREATE:     
        return {
          ...state,
          departamentos: [...state.departamentos, { ...payload }]          
        };
    case accions.DEPARTAMENTOS_UPDATE:     
        return {
          ...state,
          departamentos: state.departamentos.map(
            dep => ( dep.id === payload.id ? { ...payload } : dep )
          )        
        };
    case accions.DEPARTAMENTOS_SET_ERROR:
  
      return {
          ...state,
          error: payload,
          fetching: payload === '' ?  state.fetching : false
        };  
    case accions.DEPARTAMENTOS_SET_FILTER:
        return {
          ...state,
          filterValue: payload
        }
    case accions.DEPARTAMENTOS_DISABLE:
      return {
        ...state,
        departamentos: state.departamentos.map(
          dep => ( dep.id === payload ? { ...dep, estatus: '0' } : dep )
        )  
      }
    case accions.DEPARTAMENTOS_DISABLE_ASIG_EMPLEADO:
      return {
        ...state,
        empleados: state.empleados.map(
          emp => ( emp.Departamento[0].Departamento_Empleado.id === payload ? {} : emp )
        )  
      }

    case accions.DEPARTAMENTOS_ENABLE:
        return {
          ...state,
          departamentos: state.departamentos.map(
            dep => ( dep.id === payload ? { ...dep, estatus: '1' } : dep )
          )  
        }
    case accions.DEPARTAMENTOS_CLEAN_CATEGORIAS:
      return {
        ...state,
        deparCategorias: []
      }
    case accions.DEPARTAMENTOS_CLEAN_EMPLEADOS_ASIGNADOS:
      return {
        ...state,
        empleados: []
      }
    default:
      return state;
  }
};
export default DepartamentosReducer;
