const accions = {        
    DEPARTAMENTOS_FETCH: '[DEPARTAMENTOS]fetch all DEPARTAMENTOS',
    DEPARTAMENTOS_FETCH_CATEGORIA: '[DEPARTAMENTOS]fetch all DEPARTAMENTOS CATEGORIAS',
    DEPARTAMENTOS_UPDATE: '[DEPARTAMENTOS] update departamento',
    DEPARTAMENTOS_CREATE: '[DEPARTAMENTOS] create departamento',
    DEPARTAMENTOS_SET_ERROR: '[DEPARTAMENTOS] error',        
    DEPARTAMENTOS_SET_CHECKING: '[DEPARTAMENTOS] fetching...',
    DEPARTAMENTOS_SET_FILTER: '[DEPARTAMENTOS] set value to filter DEPARTAMENTOS',
    DEPARTAMENTOS_DISABLE: '[DEPARTAMENTOS] disable departamento',
    DEPARTAMENTOS_DISABLE_ASIG_EMPLEADO: '[DEPARTAMENTOS] disable ASIG EMPLEADO',
    DEPARTAMENTOS_ENABLE: '[DEPARTAMENTOS] enable departamento',    
    DEPARTAMENTOS_CLEAN_CATEGORIAS: '[DEPARTAMENTOS] clean categorias',
    DEPARTAMENTOS_EMPLEADOS_ASIGNADOS: '[DEPARTAMENTOS] fetch assigned employes',    
    DEPARTAMENTOS_CLEAN_EMPLEADOS_ASIGNADOS: '[DEPARTAMENTOS] clean assigned employes',    

    fetchAll: (departamentos) => {
        return{
            type: accions.DEPARTAMENTOS_FETCH,
            payload: departamentos
        }
    },
    fetchAllDepartamentoCategorias: (categorias) => {
        return{
            type: accions.DEPARTAMENTOS_FETCH_CATEGORIA,
            payload: categorias
        }
    },
    fetchAllDepartamentoEmpleados: (empleados) => {
        return{
            type: accions.DEPARTAMENTOS_EMPLEADOS_ASIGNADOS,
            payload: empleados
        }
    },
    cleanEmpleados: () =>{
        return{
            type: accions.DEPARTAMENTOS_CLEAN_EMPLEADOS_ASIGNADOS,            
        }
    },
    create: (departamento) => {
        return{
            type: accions.DEPARTAMENTOS_CREATE,
            payload: departamento
        }
    },
    update: (departamento) => {
        return{
            type: accions.DEPARTAMENTOS_UPDATE,
            payload: departamento
        }
    },
    
    setChecking: (value) => {
        return {
            type: accions.DEPARTAMENTOS_SET_CHECKING,
            payload: value
        }        
    },    
    
    setError: (error) =>{
        return {
            type: accions.DEPARTAMENTOS_SET_ERROR,
            payload: error
        }
    },
    setFilter: (value) =>{
        return {
            type: accions.DEPARTAMENTOS_SET_FILTER,
            payload: value
        }
    },
    disable: (value) =>{
        return {
            type: accions.DEPARTAMENTOS_DISABLE,
            payload: value
        }
    },
    disableAsigEmpleado: (id) =>{
        return {
            type: accions.DEPARTAMENTOS_DISABLE_ASIG_EMPLEADO,            
            payload: id
        }
    },
    enable: (value) =>{
        return {
            type: accions.DEPARTAMENTOS_ENABLE,
            payload: value
        }
    },
    cleanDeparCategorias: () =>{
        return {
            type: accions.DEPARTAMENTOS_CLEAN_CATEGORIAS,            
        }
    }
    
}

export default accions;