import authTypes from './actions';


const initState = {
  user: null,
  info: null,
  checking: true,
  message: "",
  error: "",
  loginProgress: false 
};

/**
 *
 * @todo impure state mutation/explaination
 */
const AuthReducer = (state = initState, action) => {

  const { type, payload } = action;  
  switch (type) {
    
    case authTypes.AUTH_LOGIN:      
      return {
        ...state,
        user: payload,
        checking: false,
        error: ''
      }

    case authTypes.AUTH_FETH_USER:
      return {
        ...state,
        info: payload
      }

    case authTypes.AUTH_CHECKING_FINISH:
      return {
        ...state,
        checking: false
      }

    case authTypes.AUTH_LOGOUT:
      return {
        message: "",
        loginProgress: false,
        checking: false,
        user: null,
        error: null
      }

    case authTypes.authLogoutTokenExpired:
      return {
        checking: false,
        message: "Tu Sesión Expiró, Vuelve a Iniciarla"
      }
    case authTypes.AUTH_SET_CHECKING:
      return{
        ...state,
        loginProgress: payload
      }
    case authTypes.AUTH_SET_ERROR:
      return{
        ...state,
        error: payload
      }
   
    default:
      return state;
  }
};
export default AuthReducer;
