import 'react-perfect-scrollbar/dist/css/styles.css';
import { useEffect, useReducer, useState } from 'react';
import { useRoutes } from 'react-router-dom';
import { ThemeProvider } from '@material-ui/core';
import GlobalStyles from 'src/components/GlobalStyles';
import 'src/mixins/chartjs';
import theme from 'src/theme';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Provider, useDispatch, useSelector } from "react-redux";
import { adminRoutes, publicRoutes, superAdminRoutes } from 'src/routes';
import { makeStyles } from '@material-ui/styles';
import store from "src/reducers/store";
import { startCheckingSesion } from './reducers/auth/actionCreator';
import AxiosAux from './hooks/UseAxios';

const useStyles = makeStyles(() => ({
  child:{
    position: 'absolute',
    top: '50%',
    left: '50%',    
  }
}));

const ProviderConfig = () => {
  const superAdminRouting = useRoutes(superAdminRoutes);
  const adminRouting = useRoutes(adminRoutes);
  const publicRouting = useRoutes(publicRoutes);    
  const classes = useStyles();
  const {
    doRequest
  } = AxiosAux();

  const { checking, user } = useSelector(state => state.auth);
  const dispatch = useDispatch();

  const verifySession = async () => {        
    dispatch(startCheckingSesion(doRequest))
  }

  useEffect(() => {
    verifySession();
  }, []);

  if(checking){  
    return (          
      <CircularProgress color="secondary" className={classes.child}/>      
    )
  } 

  return (
    <ThemeProvider theme={theme}>
      <GlobalStyles />
      { !user ? publicRouting : user.nivelAcceso === 0 ? superAdminRouting : adminRouting }
    </ThemeProvider>
  )
 
};

function App(){
  return (
    <Provider store={store}>
      <ProviderConfig></ProviderConfig>              
    </Provider>     
  );
}

export default App;
