import { setConfirmDialog, startSetAlert } from '../ui/actionCreator';
import actions from './actions';

const {
  fetchAll,
  fetchAllEstado,
  create,
  update,
  setError,
  setChecking,
  setFilter,
  disable,
  enable
} = actions;

export const startFetchAll = (doRequest) => {

    return async (dispatch) => {
        try {
            
            dispatch(setChecking(true));

            const { data } = await doRequest('empleado');                                           
            dispatch(fetchAll(data.empleados));            
            
        } catch (error) {                  
          if (error.response) {              
            dispatch(setError(error.response.data.message));
          } else {
            dispatch(setError('Error del Servidor'));
          }
        }
    }
};

export const startFetchAllEstado = (doRequest) => {

  return async (dispatch) => {
      try {
          
          dispatch(setChecking(true));

          const { data } = await doRequest('estado');                                           
          dispatch(fetchAllEstado(data.estados));            
          
      } catch (error) {                  
        if (error.response) {              
          dispatch(setError(error.response.data.message));
        } else {
          dispatch(setError('Error del Servidor'));
        }
      }
  }
};

export const startCreate = (values, doRequest) => {
  
  return async (dispatch) => {
    try {
      dispatch(setError(''));    
      const { data } = await doRequest('empleado', {
        ...values
      }, 'POST');

      dispatch(create(data.empleado));     

    } catch (error) {          
      throw error;
    }
  }
}

export const startUpdate = (id, values, doRequest) => {
  
  return async (dispatch) => {
    try {
      dispatch(setError(''));    
      const { data } = await doRequest(`empleado/${id}`, {
        ...values
      }, 'PUT');

      dispatch(update(data.empleado));     

    } catch (error) {           
      throw error;
    }
  }
}

export const startDisable = (id, doRequest) => {
  return async dispatch =>{
    try {
      dispatch(setError(''));    
      await doRequest(`empleado/${id}`, {
      }, 'DELETE');

      dispatch(disable(id));
      dispatch(setConfirmDialog({
        open:false,
        inProgress: false
      }));     
      dispatch(startSetAlert({
        type: 'success',
        isOpen: true,
        title: 'Éxito',
        message: 'Empleado Deshabilitado Correctamente'
      })); 

    } catch (error) {
      throw error; 
    }
  }
}

export const startEnable = (id, doRequest) => {
  return async dispatch =>{
    try {
      dispatch(setError(''));    
      await doRequest(`empleado/${id}`, {        
      }, 'PATCH');

      dispatch(enable(id));  
      dispatch(setConfirmDialog({
        open:false,
        inProgress: false
      }));     
      dispatch(startSetAlert({
        type: 'success',
        isOpen: true,
        title: 'Éxito',
        message: 'Empleado Habilitado Correctamente'
      })); 
   
    } catch (error) {
      throw error; 
    }
  }
}

export { setError, setFilter };