import { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  CircularProgress,
  Link  
} from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import StyledButton from 'src/components/controls/StyledButton';
import UsePagination from "src/hooks/UsePagination";
import StyledDialog from '../Dialog';
import CapituloForm from './CapituloForm';
import AxiosAux from 'src/hooks/UseAxios';
import ConfirmDialog from '../ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { setConfirmDialog, startSetAlert } from 'src/reducers/ui/actionCreator';
import { setError, startDisable, startEnable } from "src/reducers/capitulos/actionCreator";
import { dateFormater } from 'src/helpers/formatters';
import handleRequestError from 'src/helpers/handleReqErrors';

const useStyles = makeStyles(() => ({
  child:{
    
  },
  root:{
    textAlign: 'center'
  }
}));

const CapituloListResults = ({...rest }) => {
  const dispatch = useDispatch();
  const { capitulos, filterValue, fetching } = useSelector(state => state.capitulos);      
  const [open, setOpen] = useState(false);
  const [filteredCapitulos, setFilteredCapitulos] = useState([]);
  const [childrenDialog, setChildrenDialog] = useState({ title: "", children: null });
  const classes = useStyles();

  const {
    handleLimitChange,
    handlePageChange,
    limit,
    page
  } = UsePagination();

  const {
    doRequest
  } = AxiosAux();

  const filter = (value) => {
    if (value !== "") {
      const filtered = capitulos.filter(
        x =>
          x.id === value ||
          x.nombre.toLowerCase().includes(value) ||
          x.descripcion.toLowerCase().includes(value) ||
          x.categoria.nombre.toLowerCase().includes(value)
      );
      setFilteredCapitulos(filtered);
    } else {
      setFilteredCapitulos(capitulos);
    }

  }

  const openDialog = (capitulo) => {
    const title = 'Actualizar Capítulo';
    //Se formatea la estructura del objeto del capitulo
    capitulo.file = {
      base64: '',
      fileName: capitulo.path.split('/').pop()
    }
        
    setChildrenDialog({
      title: title,
      children: <CapituloForm setOpen={setOpen} title={title} edit={capitulo} ></CapituloForm>
    });

  }

  const startDeleteOrEnable = (id, estatus, nombre) => {
    dispatch(setConfirmDialog({
      open: true,
      title: estatus === '1' ? `Desea Deshabilitar la Capítulo ${nombre}?` : `Desea Habilitar la Capítulo ${nombre}?`,
      subtitle: "Está Acción puede ser Revertida",      
      onConfirm: () => { handleDeleteOrEnable(id, estatus); dispatch(setConfirmDialog({inProgress: true})) }
    }));
  }

  const handleDeleteOrEnable = async (id, estatus) => {
    try {
      if (estatus === '1') {
        await dispatch(startDisable(id, doRequest));
      } else {
        await dispatch(startEnable(id, doRequest));
      }               

    } catch (err) {      
      if (err?.response?.status !== 500) {
        const stringError = handleRequestError(err);
        dispatch(setError(stringError));
      }
      dispatch(startSetAlert({
        type: 'error',
        isOpen: true,
        title: 'Error',
        message: "Error del Servidor, Intentelo de Nuevo"
      }));       
      dispatch(setConfirmDialog({inProgress: false, open: false}));
    }
  }

  useEffect(() => {
    if (childrenDialog.children !== null) {
      setOpen(true);
    }
  }, [childrenDialog]);

  useEffect(() => {
    if (capitulos) {
      setOpen(false);
      setFilteredCapitulos(capitulos)
    }
  }, [capitulos]);

  useEffect(() => {
    if (filterValue !== null) {
      filter(filterValue);
    }
  }, [filterValue]);



  return (
    <Card {...rest}>
     
      <PerfectScrollbar>
        <Box sx={{ minWidth: 1050 }}>
         
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Clave capitulo
                </TableCell>
                <TableCell>
                  Nombre
                </TableCell>
                <TableCell>
                  Descripción
                </TableCell>
                <TableCell>
                  Contenido
                </TableCell>
                <TableCell>
                  Fecha de Registro
                </TableCell>             
                <TableCell>
                  Estatus
                </TableCell>
                <TableCell>
                  Acciones
                </TableCell>
              </TableRow>
            </TableHead>
            
            <TableBody>      
               
              {
                filteredCapitulos.slice(page * limit, (page + 1) * limit).map(capitulo => (
                  <TableRow key={capitulo.id}>
                    <TableCell>{capitulo.id}</TableCell>
                    <TableCell>{capitulo.nombre}</TableCell>
                    <TableCell>{capitulo.descripcion}</TableCell>
                    <TableCell>{capitulo.tipo.toUpperCase()} <br></br>  
                      <Link href={capitulo.path} target='__blank'>Ver Archivo </Link>
                      {capitulo.duracion ? `${capitulo.duracion} min.`: ''} <br></br>
                      {`FTP: dist/public/videos/${capitulo.id}/${capitulo.path.split("/").pop()}`}
                      </TableCell>
                    <TableCell>{dateFormater(capitulo.createdAt)}</TableCell>                                      
                    <TableCell>{capitulo.estatus === '1' ? <Typography variant='button' color="green">Activo</Typography> 
                                                         : <Typography variant='button' color="red">No Activo</Typography>}</TableCell>
                    <TableCell>
                      <StyledButton color='primary' onClick={() => openDialog(capitulo)} endIcon={<CreateIcon></CreateIcon>}> Editar </StyledButton>
                      {
                        capitulo.estatus === '1' ?
                          <StyledButton color='error' onClick={() => startDeleteOrEnable(capitulo.id, capitulo.estatus, capitulo.nombre)} endIcon={<DeleteIcon></DeleteIcon>}> Deshabilitar </StyledButton>
                          :
                          <StyledButton color='info' onClick={() => startDeleteOrEnable(capitulo.id, capitulo.estatus, capitulo.nombre)} endIcon={<ArrowUpward></ArrowUpward>}> Habilitar </StyledButton>
                      }
                    </TableCell>
                  </TableRow>
                ))
              }
            </TableBody>
            
          </Table>
          {
               fetching && 
                <div className={classes.root}>
                  <CircularProgress color="primary"/>    
                </div>                                
            }  
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredCapitulos.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
      <StyledDialog
        title={childrenDialog.title}
        open={open}
        setOpen={setOpen}
      >
        {childrenDialog.children}
      </StyledDialog>
      <ConfirmDialog>
      </ConfirmDialog>
    </Card>
  );
};

export default CapituloListResults;
