const files = ["pdf", "docx", "doc", "xlx", 'xlsx', 'xlsm', 'xlsb'];
const showFile = (file, ext) =>{
    let extension = '';

    if( ext !== null){
        extension = ext;
    }else{        
        extension = file.path.split('.').pop();
    }

    if(file.tipo === 1){
        return (<video src={file.path} controls></video>)
    }else if( files.includes(extension) ){
        return (<iframe src={file.path} width="600" height="300"></iframe>)
    }else{
        return (<img src={file.path} alt="Archivo Curso" width="600"></img>)
    }    
}

export{
    showFile
};