import actions from './actions';

const {
    checkingFinish,
    fetchUser,
    login,
    setChecking,
    setError,
    logOut   
} = actions;

export const startLogin = (email, password, doRequest) => {

    return async (dispatch) => {
        try {
            
            dispatch(setChecking(true));

            const { data } = await doRequest('auth/signin', {
                email,
                password
              }, 'POST');
            
            localStorage.setItem('authorization_aktkey', data.token);
            localStorage.setItem('refreshToken_aktkey', data.refreshToken);      
            //const { data } = await doRequest('auth/login', { email, password }, 'POST');            
            dispatch(login(data.user));
            
        } catch (error) {    
         
          if (error.response) {              
            dispatch(setError(error.response.data.message));
          } else {
            dispatch(setError('Error del Servidor'));
          }
        }
    }
}

export const startFetchingUser = (doRequest) => {

  return async (dispatch) => {
      try {                    
          
          const { data } = await doRequest(`adminPerfil`);                    
          dispatch(fetchUser(data.administrador));          

      } catch (error) {  
        console.log(error)             
        if (error.response) {              
          dispatch(setError(error.response.data.message));
        } else {
          dispatch(setError('Error del Servidor'));
        }
      }
  }
}

export const startCheckingSesion = (doRequest) =>{
    return async (dispatch) => {
        try {
            
            dispatch(setChecking(true));
            const { data } = await doRequest('auth/check');                         
            
            localStorage.setItem('authorization_aktkey', data.token);
            localStorage.setItem('refreshToken_aktkey', data.refreshToken);          
        
            dispatch(login(data.user));
            
        } catch (error) {   
          localStorage.removeItem('authorization_aktkey');
          localStorage.removeItem('refreshToken_aktkey');                
          dispatch(logOut());
        }
    }
}

export const startLogginOut = () => {
  return async (dispatch) => {
    try {                      
        localStorage.removeItem('authorization_aktkey');
        localStorage.removeItem('refreshToken_aktkey');              
        dispatch(logOut(data.user));        
    } catch (error) {         
      dispatch(logOut());
    }
}
}

