
import {
    Dialog,    
    DialogTitle,
    Typography,
    DialogContent
} from "@material-ui/core";
import { makeStyles } from '@material-ui/styles';
import PropTypes from 'prop-types';
import StyledButton from 'src/components/controls/StyledButton';
import CloseIcon from '@material-ui/icons/Close';
import theme from 'src/theme/index'

const useStyles = makeStyles(() => ({
    dialogWrapper: {
        padding: theme.spacing(2),
        position: 'absolute',
        top: theme.spacing(5)
    }
}));


const StyledDialog = (props) => {

    const { title, children, open, setOpen, fullWidth = false, disabled = false } = props;
    const classes = useStyles();

    return (
    <Dialog open={open} maxWidth='md' classes={!fullWidth ? { paper: classes.dialogWrapper } : {}} fullWidth={fullWidth} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">
            <div style={{ display: 'flex' }}>
                <Typography variant="h4" component="div" style={{ flexGrow: 1 }}>
                    {title}
                </Typography>
                <StyledButton
                    disabled={disabled}
                    color="danger"
                    onClick={() => setOpen(false)}                    
                >
                    <CloseIcon></CloseIcon>
                </StyledButton>
            </div>
        </DialogTitle>
        <DialogContent dividers>
            {children}
        </DialogContent>        
    </Dialog>
    )
}

StyledDialog.propTypes = {
    open: PropTypes.bool.isRequired,
    setOpen: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired
};

export default StyledDialog;