import {
  Avatar,
  Box,  
  Card,  
  CardContent,
  Typography
} from '@material-ui/core';
import { useSelector } from 'react-redux';

const AccountProfile = (props) => {
  
  const { user } = useSelector(state => state.auth);
  
  return (
    <Card {...props}>
      <CardContent>
        <Box
          sx={{
            alignItems: 'center',
            display: 'flex',
            flexDirection: 'column'
          }}
        >
          <Avatar
            src={'/static/images/avatars/avatar.png'}
            sx={{
              height: 100,
              width: 100
            }}
          />
          <Typography
            color="textPrimary"
            gutterBottom
            variant="h3"
            style={{
              marginTop: '10px'
            }}
          >
            {user.email}
          </Typography>                  
        </Box>
      </CardContent>           
    </Card>
  )
}

export default AccountProfile;
