// First we need to import axios.js
import axios from 'axios';
import LogOut from 'src/helpers/LogOut';
// Next we make an 'instance' of it
const instance = axios.create({
  // .. where we make our configurations
  baseURL: `${process.env.REACT_APP_API_URL}/api/`
});
instance.interceptors.request.use(
  (config) => {
    if(config.url.includes("refreshToken")){
      const refreshtoken =  localStorage.getItem("refreshToken_aktkey") || '';
      config.headers["access-token"] = `Bearer ${refreshtoken}`;
    }
    if (!config.headers.Authorization) {
      const token = localStorage.getItem("authorization_aktkey") || '';

      if (token) {
        config.headers.Authorization = `Bearer ${token}`;
      }
    }

    return config;
  },
  error => Promise.reject(error)
);

instance.interceptors.response.use((response) => {
  // Any status code that lie within the range of 2xx cause this function to trigger
  // Do something with response data
  return response;
}, async (error) => {
  // Any status codes that falls outside the range of 2xx cause this function to trigger
  // Do something with response error  
  if (error.response.status === 401) {    
    //logica de refresh token, refrescar token y volver a intentar la request
    try {
      const { data } = await instance.post('auth/refreshToken');      
      localStorage.setItem('authorization_aktkey', data.token);
      localStorage.setItem('refreshToken_aktkey', data.refreshToken);      
    } catch (err) {      
      return null;
    }
    error.response.data.tryAgain = true;
  } else {
    error.response.data.tryAgain = false;
  }
  return Promise.reject(error);
});

export default instance;