import { useEffect, useState } from 'react';
import {
  Box,  
  Card,
  CardContent,
  CardHeader,
  Divider,
  Grid,  
  Typography 
} from '@material-ui/core';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import SaveIcon from '@material-ui/icons/Save';
import { setError, startFetchingUser } from 'src/reducers/auth/actionCreator';
import AxiosAux from 'src/hooks/UseAxios';
import StyledTextField  from 'src/components/controls/StyledTextField';
import StyledButton from 'src/components/controls/StyledButton';
import { startUpdate } from 'src/reducers/administradores/actionCreator';

const phoneRegex = /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/;

const AccountProfileDetails = (props) => {
  
  const dispatch = useDispatch();
  const { info, error } = useSelector(state => state.auth);  
  const [values, setValues] = useState(null);

  const validate = () =>{
    return Yup.object().shape({
        persona: Yup.object().shape({  
            telefono: Yup.string().matches(phoneRegex, 'Número de Teléfono no Válido').min(10, 'Longitud mínima de 10 números').required('Teléfono es Requerido'),            
        }),                             
        usuario: Yup.object().shape({
            email: Yup.string().email('Debe ser un Correo Electrónico Válido').max(255),                   
            password:  Yup.string().length(6, 'Longitud mínima: 6 carácteres'),                            
            confirmPassword: Yup.string().length(6, 'Logitud mínima: 6 caráteres')
                                            .oneOf([Yup.ref('password'), null], 'Las Contraseñas no Coinciden')
        })                             
    });
  }

  const handleSubmit = async (values) =>{
    try {                                 
        
      await dispatch(startUpdate(values.id, values, doRequest));                                
      message = 'Perfil Actualizado Correctamente';
                                          
      dispatch(startSetAlert({
          isOpen: true,
          title: 'Éxito',
          message: message
      }));

    } catch (err) {                
        if (err?.response?.status !== 500) {
            const stringError = handleRequestError(err, setFieldError);
            dispatch(setError(stringError));
        }
        dispatch(startSetAlert({
            type: 'error',
            isOpen: true,
            title: 'Error',
            message: "Error del Servidor, Intentelo de Nuevo"
        }));                       
    }
  }

  const {
    doRequest
  } = AxiosAux();
  

  useEffect(() => {
    dispatch(startFetchingUser(doRequest));
    dispatch(setError(''));
  }, []);
  
  useEffect(() => {
    if(info){
      setValues({
        persona:{
          ...info.persona
        },        
        usuario:{
          ...info.usuario,
          password: '',
          confirmPassword: ''
        },
        id: info.id
      })
    }

  }, [info]);


  useEffect(() =>{
    console.log(values)
  }, [values])


  if(!values){
    return (
      <div>
        Cargando...
      </div>
    )
  }

  return (
      <Card>
        <CardHeader
          subheader="Puedes modificar tus credenciales de acceso"
          title="Perfil"
        />
        <Divider />
        <CardContent>
          <Formik
              initialValues={values}   
              validationSchema={
                  validate()
              }
              validateOnChange={true}    
              onSubmit={(values, { setFieldError }) => handleSubmit(values, setFieldError)}
          >
              {({
                  errors,                
                  handleBlur,
                  handleChange,
                  handleSubmit,
                  isSubmitting,
                  touched,
                  values
              }) => (

                <form onSubmit={handleSubmit}>
                      <Box sx={{ mb: 3 }}>
                          <Typography
                              color="textPrimary"
                              variant="h2"
                          >
                              Actualiza tus Datos
                          </Typography>
                      </Box>
                      <Grid container spacing={1}>
                          <Grid item xs={6}>                         
                              <StyledTextField
                                  error={Boolean(touched.persona?.nombre && errors.persona?.nombre)}
                                  helperText={touched.persona?.nombre && errors.persona?.nombre}
                                  label="Nombre"
                                  disabled={true}
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  name="nombre"
                                  value={values.persona?.nombre}
                              />              
                              <StyledTextField
                                  error={Boolean(touched.persona?.primerAp && errors.persona?.primerAp)}
                                  helperText={touched.persona?.primerAp && errors.persona?.primerAp}
                                  label="Primer Apellido"
                                  disabled={true}
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  name="primerAp"
                                  value={values.persona?.primerAp}
                              />   
                              <StyledTextField
                                  error={Boolean(touched.persona?.segundoAp && errors.persona?.segundoAp)}
                                  helperText={touched.persona?.segundoAp && errors.persona?.segundoAp}
                                  label="Segundo Apellido"
                                  disabled={true}
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  name="segundoAp"
                                  value={values.persona?.segundoAp}
                              />    
                              <StyledTextField
                                  error={Boolean(touched.persona?.telefono && errors.persona?.telefono)}
                                  helperText={touched.persona?.telefono && errors.persona?.telefono}
                                  label="Teléfono"                             
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  name="persona.telefono"
                                  value={values.persona?.telefono}
                              />                       
                          </Grid>
                          <Grid item xs={6}>
                            <StyledTextField
                                  error={Boolean(touched.usuario?.email && errors.usuario?.email)}
                                  helperText={touched.usuario?.email && errors.usuario?.email}
                                  label="Correo Electrónico"                                  
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  name="usuario.email"
                                  value={values.usuario?.email}
                              /> 
                              <StyledTextField
                                  error={Boolean(touched.usuario?.password && errors.usuario?.password)}
                                  helperText={touched.usuario?.password && errors.usuario?.password}
                                  label="Contraseña"                                  
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  name="usuario.password"
                                  value={values.usuario?.password}
                                  type='password'
                              /> 
                              <StyledTextField
                                  error={Boolean(touched.usuario?.confirmPassword && errors.usuario?.confirmPassword)}
                                  helperText={touched.usuario?.confirmPassword && errors.usuario?.confirmPassword}
                                  label="Confirmar Contraseña"                                  
                                  onBlur={handleBlur}
                                  onChange={handleChange}
                                  name="usuario.confirmPassword"
                                  value={values.usuario?.confirmPassword}
                                  type='password'
                              /> 
                          </Grid>
                      </Grid>
                      <Box sx={{ py: 2 }}>
                          <StyledButton
                              color='primary'
                              type='submit'
                              startIcon={<SaveIcon></SaveIcon>}
                              disabled={isSubmitting}
                              variant={'contained'}
                          >
                              Actualizar Perfil
                          </StyledButton>
                          <Typography color="error" align="center" style={{ marginTop: '10px' }}>
                              {error}
                          </Typography>                        
                      </Box>
                  </form>
              )}
          </Formik>         
        </CardContent>
        <Divider />
        
      </Card>
  );
};

export default AccountProfileDetails;
