import { Typography } from "@material-ui/core";

const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', hour12: true, hour: 'numeric', minute: 'numeric' };

export const moneyFormater = (number) =>{
    return new Intl.NumberFormat('es-mx', { style: 'currency', currency: 'MXN' }).format(number);
}

export const dateFormater = (date) =>{
    return new Date(date).toLocaleDateString('es-mx', options);
}

export const formatError = (data, setErrors) =>{ 
    if(data.errors){
        for (const error of data.errors) {
            setErrors(error.param, error.msg);
        }
        return "Error!";
    }else{
        return data.message; 
    }
    
}