import { useEffect, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  CircularProgress
} from '@material-ui/core';
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import CreateIcon from '@material-ui/icons/Create';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import StyledButton from 'src/components/controls/StyledButton';
import UsePagination from "src/hooks/UsePagination";
import StyledDialog from '../Dialog';
import EmpleadoForm from './EmpleadoForm';
import AxiosAux from 'src/hooks/UseAxios';
import ConfirmDialog from '../ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { setConfirmDialog, startSetAlert } from 'src/reducers/ui/actionCreator';
import { setError, startDisable, startEnable } from "src/reducers/empleados/actionCreator";
import { dateFormater } from 'src/helpers/formatters';
import handleRequestError from 'src/helpers/handleReqErrors';

const useStyles = makeStyles(() => ({
  child:{
    
  },
  root:{
    textAlign: 'center'
  }
}));

const EmpleadoListResults = ({...rest }) => {
  const dispatch = useDispatch();
  const { empleados, filterValue, fetching } = useSelector(state => state.empleados);      
  const [open, setOpen] = useState(false);
  const [filteredEmpleados, setFilteredEmpleados] = useState([]);
  const [childrenDialog, setChildrenDialog] = useState({ title: "", children: null });
  const classes = useStyles();

  const {
    handleLimitChange,
    handlePageChange,
    limit,
    page
  } = UsePagination();

  const {
    doRequest
  } = AxiosAux();

  const filter = (value) => {
    if (value !== "") {
      const filtered = empleados.filter(
        x =>
          x.id === value ||
          x.persona.nombre.toLowerCase().includes(value) ||
          x.persona.primerAp.toLowerCase().includes(value) ||
          x.usuario.email.toLowerCase().includes(value)
      );
      setFilteredEmpleados(filtered);
    } else {
      setFilteredEmpleados(empleados);
    }

  }

  const openDialog = (empleado) => {
    const title = 'Actualizar empleado';
    //Se formatea la estructura del objeto del empleado
    if (empleado.persona.segundoAp === null)
      empleado.persona.segundoAp = '';
      empleado.usuario.confirmPassword = '';
      empleado.usuario.password = '';
      if(empleado.Departamento?.length > 0){
        empleado.idDepartamento = empleado.Departamento[0].id;  
      }else{
        empleado.idDepartamento = -1;
      }
      
    setChildrenDialog({
      title: title,
      children: <EmpleadoForm setOpen={setOpen} title={title} edit={empleado} ></EmpleadoForm>
    });

  }

  const startDeleteOrEnable = (id, estatus, nombre) => {
    dispatch(setConfirmDialog({
      open: true,
      title: estatus === '1' ? `Desea Deshabilitar al empleado ${nombre}?` : `Desea Habilitar al empleado ${nombre}?`,
      subtitle: "Está Acción puede ser Revertida",      
      onConfirm: () => { handleDeleteOrEnable(id, estatus); dispatch(setConfirmDialog({inProgress: true})) }
    }));
  }

  const handleDeleteOrEnable = async (id, estatus) => {
    try {
      if (estatus === '1') {
        await dispatch(startDisable(id, doRequest));
      } else {
        await dispatch(startEnable(id, doRequest));
      }               

    } catch (err) {
      
      if (err?.response?.status !== 500) {
        const stringError = handleRequestError(err);
        dispatch(setError(stringError));
      }
      dispatch(startSetAlert({
        type: 'error',
        isOpen: true,
        title: 'Error',
        message: "Error del Servidor, Intentelo de Nuevo"
      }));       
      dispatch(setConfirmDialog({inProgress: false, open: false}));
    }
  }

  useEffect(() => {
    if (childrenDialog.children !== null) {
      setOpen(true);
    }
  }, [childrenDialog]);

  useEffect(() => {
    if (empleados) {
      setOpen(false);
      setFilteredEmpleados(empleados)
    }
  }, [empleados]);

  useEffect(() => {
    if (filterValue !== null) {
      filter(filterValue);
    }
  }, [filterValue]);



  return (
    <Card {...rest}>
     
      <PerfectScrollbar>
        <Box sx={{ minWidth: 1050 }}>
         
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>
                  Clave empleado
                </TableCell>
                <TableCell>
                  Nombre
                </TableCell>
                <TableCell>
                  Correo Electrónico
                </TableCell>
                <TableCell>
                  Teléfono
                </TableCell>
                <TableCell>
                  Dirección
                </TableCell>
                <TableCell>
                  Departamento
                </TableCell>
                <TableCell>
                  Fecha de Registro
                </TableCell>             
                <TableCell>
                  Estatus
                </TableCell>
                <TableCell>
                  Acciones
                </TableCell>
              </TableRow>
            </TableHead>
            
            <TableBody>      
               
              {
                filteredEmpleados.slice(page * limit, (page + 1) * limit).map(empleado => (
                  <TableRow key={empleado.id}>
                    <TableCell>{empleado.id}</TableCell>
                    <TableCell>{empleado.persona.nombre} {empleado.persona.primerAp} {empleado.persona.segundoAp}</TableCell>
                    <TableCell>{empleado.usuario.email}</TableCell>
                    <TableCell>{empleado.persona.telefono}</TableCell>
                    <TableCell>{empleado.calle} #{empleado.numExt} {empleado.numInt && "int." + empleado.numInt}, {empleado.colonia}, {empleado.cp}, {empleado.ciudad}, {empleado?.estado?.nombre}</TableCell>
                    <TableCell>{ empleado.Departamento?.length > 0 ? <Typography color="royalblue">{empleado.Departamento[0].nombre}</Typography> : <Typography color="red">No Asignado</Typography> }</TableCell>
                    <TableCell>{dateFormater(empleado.persona.createdAt)}</TableCell>                                      
                    <TableCell>{empleado.estatus === '1' ? <Typography variant='button' color="green">Activo</Typography> : <Typography variant='button' color="red">No Activo</Typography>}</TableCell>
                    <TableCell>
                      <StyledButton color='primary' onClick={() => openDialog(empleado)} endIcon={<CreateIcon></CreateIcon>}> Editar </StyledButton>
                      {
                        empleado.estatus === '1' ?
                          <StyledButton color='error' onClick={() => startDeleteOrEnable(empleado.id, empleado.estatus, empleado.persona.nombre)} endIcon={<DeleteIcon></DeleteIcon>}> Deshabilitar </StyledButton>
                          :
                          <StyledButton color='info' onClick={() => startDeleteOrEnable(empleado.id, empleado.estatus, empleado.persona.nombre)} endIcon={<ArrowUpward></ArrowUpward>}> Habilitar </StyledButton>
                      }

                    </TableCell>
                  </TableRow>
                ))
              }
            </TableBody>
            
          </Table>
          {
               fetching && 
                <div className={classes.root}>
                  <CircularProgress color="primary"/>    
                </div>                                
            }  
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredEmpleados.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
      <StyledDialog
        title={childrenDialog.title}
        open={open}
        setOpen={setOpen}
      >
        {childrenDialog.children}
      </StyledDialog>
      <ConfirmDialog>
      </ConfirmDialog>
    </Card>
  );
};


export default EmpleadoListResults;
