const accions = {        
    CATEGORIAS_FETCH: '[CATEGORIAS]fetch all CATEGORIAS',
    CATEGORIAS_UPDATE: '[CATEGORIAS] update categoria',
    CATEGORIAS_CREATE: '[CATEGORIAS] create categoria',
    CATEGORIAS_SET_ERROR: '[CATEGORIAS] error',        
    CATEGORIAS_SET_CHECKING: '[CATEGORIAS] fetching...',
    CATEGORIAS_SET_FILTER: '[CATEGORIAS] set value to filter CATEGORIAS',
    CATEGORIAS_DISABLE: '[CATEGORIAS] disable categoria',
    CATEGORIAS_ENABLE: '[CATEGORIAS] enable categoria',
        
    fetchAll: (categorias) => {
        return{
            type: accions.CATEGORIAS_FETCH,
            payload: categorias
        }
    },

    create: (categoria) => {
        return{
            type: accions.CATEGORIAS_CREATE,
            payload: categoria
        }
    },
    update: (categoria) => {
        return{
            type: accions.CATEGORIAS_UPDATE,
            payload: categoria
        }
    },
    
    setChecking: (value) => {
        return {
            type: accions.CATEGORIAS_SET_CHECKING,
            payload: value
        }        
    },    
    
    setError: (error) =>{
        return {
            type: accions.CATEGORIAS_SET_ERROR,
            payload: error
        }
    },
    setFilter: (value) =>{
        return {
            type: accions.CATEGORIAS_SET_FILTER,
            payload: value
        }
    },
    disable: (value) =>{
        return {
            type: accions.CATEGORIAS_DISABLE,
            payload: value
        }
    },
    enable: (value) =>{
        return {
            type: accions.CATEGORIAS_ENABLE,
            payload: value
        }
    }
    
}

export default accions;