import accions from './actions';

const initState = {
  empleados: [],
  estados: [],
  filterValue: null,
  fetching: true,
  message: '',
  error: ''  
};

/**
 *
 * @todo impure state mutation/explaination
 */
const EmpleadosReducer = (state = initState, action) => {

  const { type, payload } = action;  
  switch (type) {
    
    case accions.EMPLEADOS_FETCH:      
      return {
          ...state,
          empleados: payload,
          fetching: false
        };
    case accions.ESTADO_FETCH:      
        return {
            ...state,
            estados: payload,
            fetching: false
          };
    case accions.EMPLEADOS_CREATE:     
        return {
          ...state,
          empleados: [...state.empleados, { ...payload }]          
        };
    case accions.EMPLEADOS_UPDATE:     
        return {
          ...state,
          empleados: state.empleados.map(
            emp => ( emp.id === payload.id ? { ...payload } : emp )
          )        
        };
    case accions.EMPLEADOS_SET_ERROR:
  
      return {
          ...state,
          error: payload,
          fetching: false
        };  
    case accions.EMPLEADOS_SET_FILTER:
        return {
          ...state,
          filterValue: payload
        }
    case accions.EMPLEADOS_DISABLE:
      return {
        ...state,
        empleados: state.empleados.map(
          emp => ( emp.id === payload ? { ...emp, estatus: '0' } : emp )
        )  
      }

    case accions.EMPLEADOS_ENABLE:
        return {
          ...state,
          empleados: state.empleados.map(
            emp => ( emp.id === payload ? { ...emp, estatus: '1' } : emp )
          )  
        }
     
    default:
      return state;
  }
};
export default EmpleadosReducer;
