const accions = {        
    CAPITULOS_FETCH: '[CAPITULOS]fetch all CAPITULOS',
    CAPITULOS_UPDATE: '[CAPITULOS] update capitulo',
    CAPITULOS_CREATE: '[CAPITULOS] create capitulo',
    CAPITULOS_SET_ERROR: '[CAPITULOS] error',        
    CAPITULOS_SET_CHECKING: '[CAPITULOS] fetching...',
    CAPITULOS_SET_FILTER: '[CAPITULOS] set value to filter CAPITULOS',
    CAPITULOS_DISABLE: '[CAPITULOS] disable capitulo',
    CAPITULOS_ENABLE: '[CAPITULOS] enable capitulo',    
        
    fetchAll: (capitulos) => {
        return{
            type: accions.CAPITULOS_FETCH,
            payload: capitulos
        }
    },

    create: (capitulo) => {
        return{
            type: accions.CAPITULOS_CREATE,
            payload: capitulo
        }
    },
    update: (capitulo) => {
        return{
            type: accions.CAPITULOS_UPDATE,
            payload: capitulo
        }
    },
    
    setChecking: (value) => {
        return {
            type: accions.CAPITULOS_SET_CHECKING,
            payload: value
        }        
    },    
    
    setError: (error) =>{
        return {
            type: accions.CAPITULOS_SET_ERROR,
            payload: error
        }
    },
    setFilter: (value) =>{
        return {
            type: accions.CAPITULOS_SET_FILTER,
            payload: value
        }
    },
    disable: (value) =>{
        return {
            type: accions.CAPITULOS_DISABLE,
            payload: value
        }
    },
    enable: (value) =>{
        return {
            type: accions.CAPITULOS_ENABLE,
            payload: value
        }
    }
    
}

export default accions;