
import { useEffect, useState } from 'react';
import api from 'src/configs/service';
import { useDispatch } from 'react-redux';
import { startLogginOut } from 'src/reducers/auth/actionCreator';

export default function AxiosAux () {
    const [endSesion, setEndSesion ] = useState(false);    
    const dispatch = useDispatch();
    useEffect(() =>{
      if(endSesion){
        dispatch(startLogginOut());
      }
    }, [endSesion])
  
    const doRequest = async ( endpoint, body, method = 'GET') =>{

        try {        
            if (method === 'GET') {
                try {
                    const data = await api.get(endpoint);                                               
                    if(data === null){
                        setEndSesion(true);
                    }
                    return data;
                } catch (error) {                    
                    throw error;                                    
                }                       
            } 
            switch (method) {
                case 'POST':   
                    try {
                        const data = await api.post(endpoint, {
                            ...body
                        }); 
                        if(data === null){
                            setEndSesion(true);
                        }     
                        return data;                    
                    } catch (error) {                    
                      throw error; 
                    }         
                case 'PUT':   
                    try {
                        const data = await api.put(endpoint, {
                            ...body
                        }); 
                        if(data === null){
                            setEndSesion(true);
                        }     
                        return data;                    
                    } catch (error) {                    
                      throw error; 
                    }  
                case 'DELETE':   
                    try {
                        const data = await api.delete(endpoint); 
                        if(data === null){
                            setEndSesion(true);
                        }     
                        return data;                    
                    } catch (error) {                    
                      throw error; 
                    }
                case 'PATCH':   
                    try {
                        const data = await api.patch(endpoint); 
                        if(data === null){
                            setEndSesion(true);
                        }     
                        return data;                    
                    } catch (error) {                    
                      throw error; 
                    }    
                default:
                    throw new Error('Method unknow');
            }
            
        } catch (error) {   
                        
            if(error.message === 'Method unknow'){                
                throw error;    
            }        
            
            if (error.response.data.tryAgain) {          
                //se vuelve a realizar la peticion y se responde al actionCreator para que guarde el state                
                const data = await doRequest(endpoint, body, method);
                return data;            
            }else{
                throw error;        
            }            
            
        }
    }

    return {
        doRequest
    }
}
