const authTypes = {        
    AUTH_CHECKING: '[auth] Checking login state',
    AUTH_CHECKING_FINISH: '[auth] Finish checking login state',
    AUTH_START_LOGIN: '[auth] Start login',
    AUTH_LOGIN: '[auth] Login',    
    AUTH_LOGOUT: '[auth] Login',    
    AUTH_SET_CHECKING: '[auth] log in...',
    AUTH_SET_ERROR: '[auth] set error',
    AUTH_FETH_USER : '[auth] get info user',
    
    checkingFinish: () => {
        return{
            type: authTypes.AUTH_CHECKING_FINISH
        }
    },

    fetchUser: (user) =>{
        return {
            type: authTypes.AUTH_FETH_USER,
            payload: user
        }
    },
    
    setChecking: (value) => {
        return {
            type: authTypes.AUTH_SET_CHECKING,
            payload: value
        }        
    },    
    
    login: (user) => {
        return {
            type: authTypes.AUTH_LOGIN,
            payload: user
        }
      
    },
    setError: (error) =>{
        return {
            type: authTypes.AUTH_SET_ERROR,
            payload: error
        }
    },
    logOut: () =>{
        return {
            type: authTypes.AUTH_LOGOUT,            
        }
    },
}

export default authTypes;